<template>
  <div class="create_post_container">
    <div class="create__section">
      <div class="title__text">Create new post</div>
      <div class="generic__text required">
        <h4 class="star">*</h4>
        Required fields
      </div>
      <div class="generic_label">Post banner image</div>
      <gf-image-drop v-model="post.banner" :showError="banner__error">
        <template v-slot:helper
          >Upload image: JPG, PNG, GIF, SVG. Max 10MB
        </template>
      </gf-image-drop>
      <gf-input-field
        v-model="post.title"
        :showError="post_title_error"
        :showRequired="true"
      >
        <template v-slot:title>Title</template>
        <template v-slot:helper>e.g. "My beautiful post"</template>
      </gf-input-field>
      <!-- <gf-textarea
        v-model="post.description"
        :showError="post_description_error"
        :showRequired="true"
      >
        <template v-slot:title>Description</template>
        <template v-slot:helper
          >e.g. "This is a story about how once upon a time in..."</template
        >
      </gf-textarea> -->
      <gf-tag-input-field
        v-model="post.tags"
        :showError="tags_error"
        :setTags="post.tags"
      >
        <template v-slot:title>Post tags</template>
        <template v-slot:helper
          >Add some post tags so users can get with it faster. Eg:
          NFT.</template
        >
      </gf-tag-input-field>
      <div class="richText">
        <richTextEditor
          :toolBarConfig="toolBarConfig"
          placeHolder="Write a text"
          v-model="post.body"
          theme="snow"
          class="richTextEditor"
        ></richTextEditor>
      </div>
      <gf-action-button
        class="space_top"
        :label="'Create post'"
        @click="uploadRichText() /*createPost() sendFile*/"
      >
      </gf-action-button>
      <div ref="aaa"></div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref } from "vue";

export default {
  data() {
    return {
      // Errors
      banner__error: false,
      post_title_error: false,
      //post_description_error: false,
      post_body_error: false,
      tags_error: false,
      // Data
      post: {
        writer: "",
        banner: null,
        title: "",
        //description: "",
        body: ref(""),
        tags: [],
      },
      rawHTML: ref(""),
      toolBarConfig: [
        [{ header: [1, 2, 3] }],
        ["bold", "italic", "underline", "strike"],
        [{ list: "bullet" }],
        [{ align: [] }],
        ["link", "image"],
        [{ script: "sub" }, { script: "super" }], // superscript/subscript
        [
          {
            color: [
              "#252525",
              "#6E6E73",
              "#f2f2f2",
              "#0071DA",
              "#BB0A21",
              "#16A000",
              "#F0C918",
            ],
          },
        ], //, { background: [] }],
        ["clean"],
      ],
      images_to_upload: [],
    };
  },
  methods: {
    async readImages() {
      // Creating the body text object
      let htmlText = document.createElement("html");
      htmlText.innerHTML = this.post.body;
      // Styles
      var sheet = document.createElement("style");
      sheet.innerHTML = `
        h1 {
          font-size: 30px!important;
          font-weight: 600!important;
          line-height: 40px!important;
          text-align: left!important;
          color: #252525 !important;
        }
        h2 {
          font-size: 18px!important;
          font-weight: 600!important;
          line-height: 27px!important;
          text-align: left!important;
          color: #252525 !important;
          margin: 0px 0px 15px 0px;
        }
        .darkMode h2 {
          color: white!important;
        }
        h3, p, li {
          font-size: 16px!important;
          font-weight: 500!important;
          line-height: 24px!important;
          text-align: justify!important;
          color: #6E6E73;
        }
        .darkMode h3,
        .darkMode p,
        .darkMode li {
          color: #969899!important;
        }
        ul {
          margin: 25px 0px 15px 0px!important;
        }
        li {
          margin: 0px 0px 0px 50px;
        }
        .p-image {
          width: 100%!important;
          display:flex;
          justify-content:center;
        }
        p img {
          margin: 30px 0px 30px 0px!important;
          border-radius: 10px!important;
        }
        p a,
        h3 a {
          pointer:cursor;
          color: #0071DA;
        }
        p a:link,
        h3 a:link{
          text-decoration: none;
        }
        @media (max-width: 764px)  {
          p img {
            max-width: 100%;
            height: auto;
          }
        }
        @media (max-width: 650px) {
           li {
             margin: 0px 0px 0px 20px!important;
           }
        }
      `;
      htmlText.getElementsByTagName("head")[0].appendChild(sheet);
      //console.log("Style:", htmlText.getElementsByTagName("head"));

      // Obtaining the images to upload
      let imgs = htmlText.getElementsByTagName("img");
      this.images_to_upload = [];

      for (let i = 0; i < imgs.length; i++) {
        let file = null;

        try {
          file = this.dataURLtoFile(
            htmlText.getElementsByTagName("img")[i].src,
            "image_" + i + ".png"
          );
          this.images_to_upload.push(file);
        } catch (e) {
          console.log("All is lost");
          // Send an error
        }
        // deleting the big url
        htmlText.getElementsByTagName("img")[i].src = i;
        // Setting image class
        let clas =
          htmlText.getElementsByTagName("img")[i].parentElement.classList;
        clas.add("p-image");
        this.post.body = htmlText.outerHTML;
      }
      return htmlText.outerHTML;
    },
    async uploadRichText() {
      await this.readImages().then((htmlRAWBody) => {
        //console.log("POST BODY:", this.post.body);
        // ====================================
        // Uploading the images to the server
        // ====================================
        const bodyData = new FormData();
        // Post general info
        bodyData.append("writer", "Admin");
        bodyData.append("banner", this.post.banner, this.post.banner.name);
        bodyData.append("title", this.post.title);
        bodyData.append("body", htmlRAWBody); //this.rawHTML);
        bodyData.append("tags", JSON.stringify(this.post.tags));

        // Post body images upload
        for (let i = 0; i < this.images_to_upload.length; i++) {
          bodyData.append(
            "image",
            this.images_to_upload[i],
            this.images_to_upload[i].name
          );
        }

        // Uploading the info to server and reciving the new images url
        // let url = "http://127.0.0.1:8000/";
        let url = this.$store.state.gf_contracts.backend;
        // let url = "http://192.168.0.21:8000/";
        let s3Url = "https://grifenixs3.s3.amazonaws.com/";
        //let media_url = "media/";

        // Token
        const token = this.$store.state.user_auth_w2.token;
        axios.defaults.headers.common["Authorization"] = "Token " + token;
        //await
        axios.post(url + "/createpostimg/", bodyData).then((res) => {
          //console.log("Response data:", res.data);
          // Updating the image link in body
          for (let j = 0; j < Object.keys(res.data.images).length; j++) {
            let newHTML = document.createElement("html");
            newHTML.innerHTML = this.post.body;
            // Setting the new img link
            newHTML.getElementsByTagName("img")[j].src =
              s3Url + res.data.images[j];
            this.post.body = newHTML.outerHTML;
          }

          // ==================================================
          // Updating the post body with the new img url info
          // ==================================================
          const newBody = new FormData();
          newBody.append("body", this.post.body);
          axios.defaults.headers.common["Authorization"] = "Token " + token;
          axios
            .put(url + "/post/" + res.data.post_id + "/update_body", newBody)
            .then((res) => {
              if (res) {
                console.log("Updated!");
              }
              //console.log(res);
              const notification = {
                type: "success",
                message: "Your post was created",
              };
              this.$store.dispatch("notification/add", notification, {
                root: true,
              });
              this.$router.push("/blog");
            });
        });
      });
    },
    dataURLtoFile(dataurl, filename) {
      var arr = dataurl.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, { type: mime });
    },
    // ================================
    // The old functions
    // ================================
    async createPost() {
      const fd = new FormData();
      fd.append("writer", "admin");
      fd.append("banner", this.post.banner, this.post.banner.name);
      fd.append("title", this.post.title);
      fd.append("description", this.post.description);
      fd.append("tags", JSON.stringify(this.post.tags));
      // let url = "http://172.20.10.2:8000";
      let url = this.$store.state.gf_contracts.backend;
      const token = this.$store.state.user_auth_w2.token;
      axios.defaults.headers.common["Authorization"] = "Token " + token;
      await axios.post(url + "/post/create", fd).then((res) => {
        if (res) {
          console.log("Post created!");
        }
        this.$router.push("/blog");
      });
    },
  },
  beforeCreate() {
    // this.$store.commit("user_auth_w2/initializeStore");
    this.$store.dispatch("user_auth_w2/initializeStoreIfExists", {
      root: true,
    });
    const token = this.$store.state.user_auth_w2.token;
    if (token) {
      axios.defaults.headers.common["Authorization"] = "Token " + token;
    } else {
      axios.defaults.headers.common["Authorization"] = "";
      // this.$store.commit("user_auth_w2/removeToken");
      this.$store.dispatch("user_auth_w2/removeToken", {
        root: true,
      });
      const notification = {
        type: "error",
        message: "Your session has expired",
      };
      this.$store.dispatch("notification/add", notification, {
        root: true,
      });
      this.$router.push("/login");
    }
  },
};
</script>

<style scoped>
.space_top {
  margin: 30px 0px 0px 0px;
}
.star {
  font-weight: 700;
  color: #c70039;
  margin: 0px 4px;
  padding: 0px;
}
.required {
  margin: 0px 0px 20px 0px !important;
}
.generic_label {
  display: flex;
  padding: 0px 10px;
  color: #6e6e75;
  cursor: text;
  z-index: 90;
  font-size: 14px;
  margin: 20px 0px 5px 0px;
}
.title__text {
  text-align: left;
  margin: 0px 0px 50px 0px;
  font-size: 30px;
  line-height: 40px;
  font-weight: 600;
  color: #252525;
}
.generic__text {
  font-size: 14px;
  margin: 0px 0px 5px 0px;
}
.subtitle__text,
.generic__text {
  text-align: left;
  display: flex;
  padding: 0px;
}
.subtitle__text {
  margin: 30px 0px 5px 0px;
  align-items: center;
}
.create_post_container {
  height: fit-content;
  width: 100%;
  max-width: 760px;
  background-color: white;
  display: flex;
  justify-content: center;
  padding: 0px 10px;
}
.create__section {
  background-color: white;
  width: 100%;
  min-width: 40%;
  height: fit-content;
  margin-top: 150px;
  margin-bottom: 150px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

@media (max-width: 450px) {
  .create__section {
    width: 100%;
    min-width: 100%;
    padding: 5%;
  }
}
</style>
