<template>
  <div
    class="container"
    v-if="show_delete_confirm === true"
    @click.prevent="closeTab(false)"
  >
    <div class="confirm_container">
      <div class="confirm_image">
        <img src="@/assets/icons/helpers/sure.svg" alt="" />
      </div>
      <div class="confirm_text">
        Are you sure you want to send a mail to
        <span>'{{ item_email }}'</span>
        with the link for the whitelist activation?
      </div>
      <div class="confirm_container_cta">
        <gf-delete-button
          :label="'No, return'"
          @click.prevent="closeTab(false)"
        ></gf-delete-button>
        <gf-accept-button
          :label="'Yes, delete'"
          @click.prevent="mailItem()"
        ></gf-accept-button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "gf-confirm-mail-whitelist",
  props: {
    show: Boolean,
    item: Object,
  },
  data() {
    return {
      show_delete_confirm: true,
      // To delete
      //   uri: "172.20.10.2",
      uri: "192.168.0.12",
    };
  },
  methods: {
    closeTab(deleted) {
      if (deleted === true) {
        this.$emit("update:modelValue", true);
      } else {
        this.$emit("update:modelValue", false);
      }
    },
    async mailItem() {
      if (!this.item) {
        return;
      }
      try {
        const bodyData = new FormData();
        if (!this.item.email) {
          return;
        }
        bodyData.append("email", this.item.email);
        const url = this.$store.state.gf_contracts.backend;
        const uri = url + "/mail_whitelist/";
        axios.defaults.headers.common["Authorization"] =
          "Token " + this.$store.state.user_auth_w2.token;
        await axios.post(uri, bodyData).then((res) => {
          this.$emit("itemSent", res.data.sent);
        });
      } catch (e) {
        console.log("Something went wrong with the deletion");
      }
    },
  },
  mounted() {
    this.show_delete_confirm = this.show;
  },
  watch: {
    show: function (v) {
      this.show_delete_confirm = v;
    },
    item: function (q) {
      console.log("ITEM NEW:", q);
    },
  },
  computed: {
    item_email: function () {
      return this.item ? this.item.email : "";
    },
    item_id_to_delete: function () {
      console.log("Item:"), this.item.id;
      return this.item ? this.item.id : null;
    },
  },
};
</script>

<style scoped>
.container {
  position: fixed;
  left: 0px;
  top: 0px;
  margin: 0px;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 9997;
  display: flex;
  justify-content: center;
  align-items: center;
}
.confirm_container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 400px;
  height: fit-content;
  background: white;
  border-radius: 16px;
  box-sizing: border-box;
  padding: 30px;
  margin: 10px;
  z-index: 9999;
}
.confirm_image {
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;
  height: fit-content;
}
.confirm_image img {
  height: 80px;
  width: 80px;
}
.confirm_text,
.confirm_text h1 {
  position: relative;
  display: flex;
  justify-content: center;
  text-align: center;
  color: #6e6e73;
  font-size: 16px;
  font-weight: 500;
  margin: 20px 0px 20px 0px;
  flex-wrap: wrap;
}
.confirm_text span {
  color: #0071da;
  font-weight: 600;
  cursor: pointer;
}
.confirm_container_cta {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  margin: 10px 0px 0px 0px;
}
</style>
