            //         var cookie = cookies[i].split('=')
            //         console.log("RAW TOKEN:", cookie)
            //         console.log("DECODED TOKEN:", decodeURIComponent(cookie[1]))
            //         if ('_gf-token' == cookie[0].trim()) {
