import axios from "axios";

export const namespaced = true

export const state = {
    displayed_posts: [], // Posts to be displayed on the web
    tag2post: {}, // {POST_ID: [TAGS]}

    // New data
    all_posts: [], // All posts without filtering
    BATCH_SIZE: 6, // Maximum batch size
    MAX_BATCHES: 0, // How many batches will have the posts posts.length/Max_Batch
    CURRENT_BATCH: 0, // We will start with batch 0, and then will be calling for more if user will need it
    POSTS: [], // Posts for the selected group from where the batches will be loaded
}

export const getters = {
    // -> getters['posts/getX']
}

export const mutations = {
    // -> commit('posts/mutationX') Synchronous
    // Sets again the displayed posts to All posts
    setAllPosts(state) {
        state.displayed_posts = state.all_posts
    },
    // Looks how many batches the loaded posts have
    setBatchesNumber(state, posts) {
        let batches = Math.floor(posts.length / state.BATCH_SIZE)
        if (posts.length % state.BATCH_SIZE > 0) {
            batches += 1
        }
        state.MAX_BATCHES = batches
    },
}

export const actions = {
    // -> dispatch('posts/actionX') Asynchronous
    async downloadAllPosts({ state, dispatch, rootState }) {
        // Downloading all posts if not downloaded
        if (state.all_posts && state.all_posts.length === 0) {
            let backend = rootState.gf_contracts.backend;
            let url = backend + "/posts/"
            axios.defaults.headers.common["Authorization"] = ""
            await axios.get(url).then((res) => {
                state.all_posts = [...res.data]
                    //state.displayed_posts = [...res.data]
                dispatch("posts/loadFirstBatch", [...res.data], { root: true })
            })
        } else {
            dispatch("posts/loadFirstBatch", state.all_posts, { root: true })
        }
        // Set the first batch
    },
    // Loads the new batch
    loadFirstBatch({ state, commit }, posts) {
        commit("setBatchesNumber", posts)
        let ini = 0
        let end = state.BATCH_SIZE
        state.displayed_posts = []
        state.CURRENT_BATCH = 0 // Resetting the current batch
        state.POSTS = posts // Setting the new posts from where to load
        if (state.CURRENT_BATCH * state.BATCH_SIZE + state.BATCH_SIZE >= posts.length) {
            end = posts.length
        }
        // Adding the new batch
        for (let i = ini; i < end; i++) {
            state.displayed_posts.push(state.POSTS[i])
        }
        state.CURRENT_BATCH += 1
    },
    // Loading one more batche from the selected posts
    loadOneMoreBatch({ state }) {
        if (state.CURRENT_BATCH >= state.MAX_BATCHES) {
            return
        }
        let ini = state.CURRENT_BATCH * state.BATCH_SIZE
        let end = state.CURRENT_BATCH * state.BATCH_SIZE + state.BATCH_SIZE
        if (end >= state.POSTS.length) {
            end = state.POSTS.length
        }
        for (let i = ini; i < end; i++) {
            state.displayed_posts.push(state.POSTS[i])
        }
        state.CURRENT_BATCH += 1
    },

    // Creates a dictionary with Post ID to TAGS list
    async setPostToTag({ state, rootState }) {
        let backend = rootState.gf_contracts.backend;
        let url = backend + "/posts/"
        axios.defaults.headers.common["Authorization"] = ""
        await axios.get(url).then((res) => {
            res.data.forEach((p => {
                let tags = JSON.parse(p.tags)
                tags.forEach((tag) => {
                    try {
                        if (state.tag2post[String(tag)] === undefined) {
                            state.tag2post[String(tag)] = []
                        }
                        state.tag2post[String(tag)].push(p.id)
                    } catch (e) {
                        console.log(e)
                    }
                })
            }))
        })
    },
    /** Appends the filtered downloaded posts from the 
     * tag list to the post list, then downloads the 
     * required IDs from the tag list if needed
     * */
    getPostsByTag({ state, dispatch }, tag) {
        state.displayed_posts = []
            // Filtering posts from all the downloaded posts if exists
        if (state.tag2post[tag]) {
            let posts_ids = [...state.tag2post[tag]]
            state.POSTS = []
            state.all_posts.forEach((post) => {
                if (posts_ids.includes(post.id)) {

                    state.POSTS.push(post)

                }
            })
            dispatch("posts/loadFirstBatch", state.POSTS, { root: true })

        } else {
            console.log("There are no posts for that tag")
        }
    }
}