<template>
  <section
    class="settings"
    :class="{ darkMode: $store.state.navbar_state.dark_mode === true }"
  >
    <div class="settings_vue">
      <gf-settings-menu :selected_link="'Update password'"></gf-settings-menu>
      <div class="container_for_settings">
        <div class="settings_container">
          <div class="log_in_container">
            <div class="login_container">
              <div class="logo_img">
                <img src="@/assets/icons/grifenix/privacy.png" alt="" />
              </div>
              <h1 class="title_text">Update password</h1>
              <gf-input-field
                v-model="old_password"
                :text_type="'password'"
                :showError="p_error_o"
                :showRequired="true"
                @input="reset()"
              >
                <template v-slot:title>Old password</template>
                <template v-slot:helper>Hopefully is not 1234</template>
              </gf-input-field>
              <gf-input-field
                v-model="password1"
                :text_type="'password'"
                :showError="p_error1"
                :showRequired="true"
                @input="reset()"
              >
                <template v-slot:title>New password</template>
                <template v-slot:helper>Hopefully is not 1234</template>
              </gf-input-field>
              <gf-input-field
                v-model="password2"
                :text_type="'password'"
                :showError="p_error2"
                :showRequired="true"
                @input="reset()"
              >
                <template v-slot:title>Repeat new password</template>
                <template v-slot:helper>Hopefully is not 1234</template>
              </gf-input-field>
              <div class="cta">
                <gf-action-button
                  class="login"
                  @click="changePWD()"
                  :label="'Update password'"
                ></gf-action-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
  <fut></fut>
</template>

<script>
import axios from "axios";
import fut from "@/components/Footer";
export default {
  components: {
    fut,
  },
  data() {
    return {
      filter_active: false,
      old_password: "",
      password1: "",
      password2: "",
      p_error_o: false,
      p_error1: false,
      p_error2: false,
    };
  },
  methods: {
    showFilter() {
      console.log("Hi");
      this.filter_active = !this.filter_active;
    },
    goto(link) {
      //window.location.href = link;
      window.open(link, "_blank");
    },
    scrollToTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    },
    reset() {
      this.p_error_o = false;
      this.p_error1 = false;
      this.p_error2 = false;
    },
    plsFillBlanks() {
      const notification = {
        type: "error",
        message: "Please fill in all the required fields!",
      };
      this.$store.dispatch("notification/add", notification, {
        root: true,
      });
    },
    checkBlanks() {
      let ready = true;
      // Checking the nulls in fields
      if (this.old_password === "") {
        this.p_error_o = true;
        this.plsFillBlanks();
        ready = false;
      } else {
        this.p_error_o = false;
      }
      if (this.password1 === "") {
        this.p_error1 = true;
        this.plsFillBlanks();
        ready = false;
      } else {
        this.p_error1 = false;
      }
      if (this.password2 === "") {
        this.p_error2 = true;
        this.plsFillBlanks();
        ready = false;
      } else {
        this.p_error2 = false;
      }
      return ready;
    },
    checkSamePWD() {
      if (this.password1.length != this.password2.length) {
        this.p_error1 = true;
        this.p_error2 = true;
        const notification = {
          type: "error",
          message: "The passwords don't match, please check them!",
        };
        this.$store.dispatch("notification/add", notification, {
          root: true,
        });
        return false;
      } else {
        return true;
      }
    },
    checkNewPWDLen() {
      const MIN_PWD_LEN = 8;
      if (this.password1.length < MIN_PWD_LEN) {
        const notification = {
          type: "error",
          message: "Password should have minimum 8 characters!",
        };
        this.$store.dispatch("notification/add", notification, {
          root: true,
        });
        return false;
      } else {
        return true;
      }
    },
    checkData() {
      let ready;
      // Fill blanks
      ready =
        this.checkBlanks() && this.checkSamePWD() && this.checkNewPWDLen();
      return ready;
    },
    async changePWD() {
      if (this.checkData() === false) {
        return;
      }
      const bodyData = new FormData();
      bodyData.append("old_password", this.old_password);
      bodyData.append("password", this.password1);
      bodyData.append("password2", this.password2);
      const token = this.$store.state.user_auth_w2.token;
      const url = this.$store.state.gf_contracts.backend;
      axios.defaults.headers.common["Authorization"] = "Token " + token;
      await axios.put(url + "/changepwd/", bodyData).then((res) => {
        const e = res.data.error;
        this.p_error_o = false;
        this.p_error1 = false;
        this.p_error2 = false;
        if (e === "BOP") {
          this.p_error_o = true;
          const notification = {
            type: "error",
            message: "Old password is incorrect!",
          };
          this.$store.dispatch("notification/add", notification, {
            root: true,
          });
        } else if (e === "SP") {
          this.p_error1 = true;
          this.p_error2 = true;
          const notification = {
            type: "error",
            message: "The new password does not match!",
          };
          this.$store.dispatch("notification/add", notification, {
            root: true,
          });
        } else if (e === "NP") {
          const notification = {
            type: "success",
            message: "Your password was changed successfully!",
          };
          this.$store.dispatch("notification/add", notification, {
            root: true,
          });
          this.$router.push("/admin_panel");
        }
      });
    },
  },
  mounted() {
    this.scrollToTop();
  },
  beforeCreate() {
    // this.$store.commit("user_auth_w2/initializeStore");
    this.$store.dispatch("user_auth_w2/initializeStoreIfExists", {
      root: true,
    });
    const token = this.$store.state.user_auth_w2.token;
    if (token) {
      axios.defaults.headers.common["Authorization"] = "Token " + token;
    } else {
      axios.defaults.headers.common["Authorization"] = "";
      //   this.$store.commit("user_auth_w2/removeToken");
      this.$store.dispatch("user_auth_w2/removeToken", {
        root: true,
      });
    }
    // if (!this.$store.state.user_auth_w2.isAuthentificated) {
    //   this.$router.push("/");
    // }
  },
};
</script>

