<template>
  <div class="register_container">
    <div class="register_form">
      <div class="logo_img">
        <img
          src="@/assets/icons/grifenix/gf_signin_white.svg"
          alt=""
          v-if="$store.state.navbar_state.dark_mode === true"
        />
        <img src="@/assets/icons/grifenix/gf_signin_blue.svg" alt="" v-else />
      </div>
      <gf-input-field
        v-model="username"
        :text_type="'text'"
        :showError="e_username"
        :showRequired="true"
        @input="reset()"
      >
        <template v-slot:title>Username</template>
        <template v-slot:helper>Is your unique way to log in.</template>
      </gf-input-field>
      <gf-input-field
        v-model="password"
        :text_type="'password'"
        :showError="e_password"
        :showRequired="true"
        :minSpace="true"
        @input="reset()"
      >
        <template v-slot:title>Password</template>
        <template v-slot:helper>Minimum 8 characters</template>
      </gf-input-field>
      <div class="cta">
        <gf-action-button
          class="login"
          @click="loginUser()"
          :label="'Log In'"
        ></gf-action-button>
      </div>
    </div>
    <div class="already_registered">
      <div class="terms_conds">
        You don't have an account?
        <span @click="this.$router.push('/accounts/signup')">Sign Up</span>.
      </div>
    </div>
  </div>
  <fut></fut>
</template>

<script>
import axios from "axios";
import fut from "@/components/Footer";
export default {
  components: {
    fut,
  },
  data() {
    return {
      username: "",
      password: "",
      // Errors
      e_username: false,
      e_password: false,
    };
  },
  methods: {
    scrollToTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    },
    reset() {
      this.e_username = false;
      this.e_password = false;
    },
    async loginUser() {
      const formData = {
        username: this.username,
        password: this.password,
      };
      axios.defaults.headers.common["Authorization"] = "";
      axios
        .post(this.$store.state.gf_contracts.backend + "/login/", formData)
        .then((response) => {
          const token = response.data.token;
          // this.$store.commit("user_auth_w2/setToken", token);
          this.$store.dispatch("user_auth_w2/setAuthToken", token, {
            root: true,
          });
          axios.defaults.headers.common["Authorization"] = "Token " + token;
          const notification = {
            type: "success",
            message: "Welcome!",
          };
          this.$store.dispatch("notification/add", notification, {
            root: true,
          });

          this.$router.push("/account");
        })
        .catch(() => {
          const notification = {
            type: "error",
            message: "Incorrect username and/or password",
          };
          this.$store.dispatch("notification/add", notification, {
            root: true,
          });
          this.e_username = true;
          this.e_password = true;
        });
    },
  },
  created() {
    this.scrollToTop();
  },
  beforeCreate() {
    // this.$store.commit("user_auth_w2/initializeStore");
    this.$store.dispatch("user_auth_w2/initializeStoreIfExists", {
      root: true,
    });
    const token = this.$store.state.user_auth_w2.token;
    if (token) {
      axios.defaults.headers.common["Authorization"] = "Token " + token;
    } else {
      axios.defaults.headers.common["Authorization"] = "";
      // this.$store.commit("user_auth_w2/removeToken");
      this.$store.dispatch("user_auth_w2/removeToken", { root: true });
    }
  },
};
</script>

<style scoped>
h1 {
  font-size: 30px;
  line-height: 40px;
  color: #252525;
  font-weight: 600;
  margin: 0px 0px 0px 0px;
}
.terms_conds {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #6e6e73;
  margin: 20px 0px 20px 0px;
}
.terms_conds span {
  box-sizing: border-box;
  padding: 0px 0px !important;
  text-decoration: none;
  color: #0071da;
  font-weight: 500;
  cursor: pointer;
}
.cta {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
}
.login {
  margin: 20px 0px 0px 0px;
}
.register_container {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: fit-content;
  min-height: 100vh;
  background-color: #f7f7f7;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 0px 10px 0px 10px;
}
.register_form,
.already_registered {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 450px;
  height: fit-content;
  background-color: white;
  box-sizing: border-box;
  justify-content: center;
  text-align: center;
  padding: 40px;
  margin: 100px 0px 0px 0px;
  border-radius: 16px;
  border: 1px solid #f2f2f2;
  -webkit-box-shadow: 5px 5px 10px 3px rgba(110, 110, 115, 0.03);
  -moz-box-shadow: 5px 5px 10px 3px rgba(110, 110, 115, 0.03);
  box-shadow: 5px 5px 10px 3px rgba(110, 110, 115, 0.03);
}
.already_registered {
  margin: 20px 0px 100px 0px;
}
.already_registered .terms_conds {
  margin: 0px;
}
.register_form > .container.form {
  margin: 10px 0px 0px 0px !important;
}
.logo_img {
  display: flex;
  justify-content: center;
  width: 100%;
  /* height: 130px; */
  box-sizing: border-box;
  padding: 10px 30px 30px 30px;
}
.logo_img img {
  width: 100%;
  max-width: 250px;
  height: auto;
  display: block;
  object-fit: contain;
}

@media (max-width: 467px) {
}
</style>
