import axios from "axios";

export const namespaced = true

export const state = {
    MAX_BATCHES: 1, // The maximum batches we can call from the server
    CURRENT_BATCH: 0, // We will start with batch 0, and then will be calling for more if user will need it
    blog_digests: { 0: [] }, // Will contain all the batches, so if something will go wrong, we can catch the problem
    digests: [] // Published digests on web
}

export const getters = {
    // -> getters['digests/getX']
}

export const mutations = {
    // -> commit('posts/mutationX') Synchronous
    getDigestPosts(state) {
        let posts = [];
        if (state.CURRENT_BATCH > state.MAX_BATCHES) {
            return
        }
        for (let i = 0; i < state.CURRENT_BATCH; i++) {
            try {
                posts.push(...state.blog_digests[i])
            } catch (e) {
                console.log("Post batch unavaible")
            }
        }
        state.digests = posts
    },
}

export const actions = {
    // -> dispatch('posts/actionX') Asynchronous
    async setFirstBatch({ state, rootState, commit }) {
        if (state.CURRENT_BATCH > 0) {
            return // If we loaded the first batch, we dont load more
        }
        let backend = rootState.gf_contracts.backend;
        let url = backend + "/digests/batch/0/get/"
        axios.defaults.headers.common["Authorization"] = ""
        await axios.get(url).then((res) => {
            state.blog_digests[state.CURRENT_BATCH] = res.data.data
            state.MAX_BATCHES = res.data.max_batches
            state.CURRENT_BATCH += 1
        })
        commit("getDigestPosts")
    },
    async setOneMoreBatch({ state, commit, rootState }) {
        let backend = rootState.gf_contracts.backend;
        let url = backend + "/digests/batch/" + Number(state.CURRENT_BATCH) + "/get/"
        axios.defaults.headers.common["Authorization"] = ""
        await axios.get(url).then((res) => {
            state.blog_digests[state.CURRENT_BATCH] = res.data.data
            state.MAX_BATCHES = res.data.max_batches
            state.CURRENT_BATCH += 1
            commit("getDigestPosts")
        })
    },
}