<template>
  <div class="drop_container">
    <div class="select_photo_container" v-if="!this.file">
      <input
        type="file"
        accept="image/*"
        class="input_hidden"
        ref="file"
        @change="change"
        @dragover="dragover"
        @dragleave="dragleave"
      />
      <div class="select_img_container" @click="browse()">
        <div v-if="!isOver">
          <img
            src="@/assets/icons/navbar/profile.svg"
            alt="IMG"
            class="default_img"
          />
          <span class="generic_text">Choose File</span>
        </div>
        <div v-else>
          <h2 class="generic__text">Drop your file here</h2>
        </div>
      </div>
    </div>
    <div class="selected_photo_container" v-if="this.file">
      <img :src="src" alt="IMG" class="img_to_post" />
      <div class="delete_file" @click="deleteFile">
        <svg
          width="16"
          height="16"
          viewBox="0 0 32 32"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <rect
            x="4.68628"
            y="24.4852"
            width="28"
            height="4"
            rx="2"
            transform="rotate(-45 4.68628 24.4852)"
            fill="#6e6e75"
          />
          <rect
            x="7.51465"
            y="4.68628"
            width="28"
            height="4"
            rx="2"
            transform="rotate(45 7.51465 4.68628)"
            fill="#6e6e75"
          />
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "gf-image-drop-updatable",
  props: {
    value: File,
    defaultURL: String,
  },
  data() {
    return {
      src: this.defaultURL,
      file: null,
      isOver: false,
      // Image data
      ALLOWED_TYPES: [],
      TYPE_TO_EXTENSION: {
        "img/png": ".png",
      },
    };
  },
  methods: {
    dragover(event) {
      this.isOver = true;
      event.stopPropagation(); // might not be necessary
      event.preventDefault();
    },
    dragleave(event) {
      this.isOver = false;
      event.stopPropagation(); // might not be necessary
      event.preventDefault();
    },
    deleteFile() {
      this.file = null;
      this.isOver = false;
      this.$emit("update:modelValue", this.file);
    },

    browse() {
      this.$refs.file.click();
    },
    getFileFromURL(url) {
      console.log("URL to download the image:", url);
      if (!url) {
        this.file = null;
        return;
      }
      try {
        fetch(url).then(async (response) => {
          console.log("Response inside:", response);
          const contentType = response.headers.get("content-type");
          const blob = await response.blob();
          const file = new File([blob], "img.png", { contentType });
          console.log("Blob inside:", blob);
          console.log("File inside:", file, "Cont type:", contentType);
          // Returning the file
          if (blob) {
            this.file = file;
            this.$emit("update:modelValue", file);
          } else {
            this.file = null;
            this.$emit("update:modelValue", file);
          }
        });
      } catch (e) {
        console.log("Imposible to download the image");
      }
    },
    change(e) {
      console.log("F");
      this.file = e.target.files[0];
      this.$emit("update:modelValue", this.file);

      try {
        let reader = new FileReader();
        reader.readAsDataURL(e.target.files[0]);
        reader.onload = (e) => {
          this.src = e.target.result;
        };
      } catch (e) {
        console.log("Error");
        this.src = this.defaultURL;
      }
    },
  },
  created() {
    console.log("Default: ", this.defaultURL);
    // this.file = this.defaultURL;
    this.getFileFromURL(this.defaultURL);
    // this.getFileFromURL(this.defaultURL);
    // this.src = this.defaultURL; // to delete
  },
  watch: {
    defaultURL: function (e) {
      console.log("Updated:", e);
      this.getFileFromURL(e);
      this.src = e;
      // this.file = e; //this.getFileFromURL(e);
      // try {
      //   let reader = new FileReader();
      //   reader.readAsDataURL(e);
      //   reader.onload = (e) => {
      //     this.src = e.target.result;
      //     this.getFileFromURL(this.src);
      //   };
      // } catch (e) {
      //   this.src = this.defaultURL;
      // }
    },
  },
};
</script>

<style scoped>
.drop_container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: fit-content;
  width: fit-content;
  max-height: 400px;
  max-width: 450px;
  cursor: pointer;
  border: 2px dashed #ebe9e9; /*#f2f2f2;*/
  border-radius: 16px;
  box-sizing: border-box;
  padding: 5px;
  transition-duration: 0.3s;
}
/* When you need to select the photo */
.select_photo_container {
  position: relative;
  height: 100%;
  min-height: 250px;
  width: 100%;
  min-width: 350px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-sizing: border-box;
  padding: 10px;
}
.input_hidden {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0px;
  left: 0px;
  border-radius: 10px;
  background-color: aqua;
  opacity: 0;
  cursor: pointer;
}
.select_img_container {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  transition-duration: 0.3s;
  pointer-events: none;
}
.select_img_container div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
}
.select_img_container {
  background-color: none;
  transition-duration: 0.3s;
}

/* When photo is selected */
.selected_photo_container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  padding: 20px 25px;
}
.img_to_post {
  position: relative;
  object-fit: contain;
  height: 100%;
  width: 100%;
  border-radius: 10px;
  transition-duration: 0.3s;
}
.delete_file {
  position: absolute;
  z-index: 9;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  right: 5px;
  top: 5px;
  background-color: white;
  border: 2px solid #ebe9e9; /*#f2f2f2;*/
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
}
.delete_file svg rect {
  transition-duration: 0.3s;
}

@media (hover: hover) {
  .delete_file:hover svg rect {
    fill: #bb0a21;
    transition-duration: 0.2s;
  }
  .input_hidden:hover ~ .select_img_container {
    background-color: #ebe9e9;
    transition-duration: 0.3s;
  }
  .input_hidden:hover {
    border: 2px dashed #f2f2f2;
    transition-duration: 0.3s;
  }
}
</style>
