<template>
  <div class="app" :class="{ darkMode: $store.state.navbar_state.dark_mode }">
    <navbar :dark_mode="$store.state.navbar_state.dark_mode"></navbar>
    <router-view> </router-view>
    <NotificationContainer />
  </div>
</template>

<script>
import Navbar from "@/components/Navbar";
import NotificationContainer from "@/components/NotificationContainer.vue";
import axios from "axios";
// SEO
// import { useMeta } from "vue-meta";

export default {
  components: {
    Navbar,
    NotificationContainer,
  },
  data() {
    return {
      site: "www.grifenix.com/",
      nightMode: false,
    };
  },
  methods: {
    // BETA

    // OLD
    accountsChanged() {
      /* If Account is changed we should proove
      if some account exists,if yes we will change it if not,
      we will close the session*/
      this.$store
        .dispatch("web3_wallet/web3AccountsChanged", { root: true })
        .then((addr) => {
          console.log("Changed: ", addr);
        });

      // this.$store
      //   .dispatch("user_account/getMetamaskAddress", null, { root: true })
      //   .then((addr) => {
      //     if (typeof addr === "undefined") {
      //       this.$store.dispatch("user_account/logOutAccount", null, {
      //         root: true,
      //       });
      //     } else {
      //       this.$store.dispatch(
      //         "user_account/updateAccount",
      //         this.$store.getters.getWalletConnected || "metamask",
      //         {
      //           root: true,
      //         }
      //       );
      //     }
      //   });
    },
    chainChanged(newChain) {
      this.$store.dispatch(
        "web3_wallet/web3NetworksChanged",
        Number(newChain),
        { root: true }
      );

      this.$store
        .dispatch("user_account/getMetamaskAddress", null, {
          root: true,
        })
        .then(() => {
          this.$store
            .dispatch("user_account/getNetwork", null, {
              root: true,
            })
            .then((netw) => {
              if (netw != "mainnet") {
                console.log("Yo are not on the main net");
                // use the main net pls
                this.$store.dispatch(
                  "notification/add",
                  {
                    type: "error",
                    message:
                      "You are using a wrong network! Please switch to the 'Mainnet'!",
                  },
                  {
                    root: true,
                  }
                );
                this.$store.dispatch(
                  "user_account/updateAccount",
                  this.$store.state.user_account.defaultWallet,
                  { root: true }
                );
              } else {
                this.$store.dispatch(
                  "user_account/updateAccount",
                  this.$store.state.user_account.defaultWallet,
                  { root: true }
                );
              }
              console.log("Network updated to: ", netw);
            });
        });
    },
    metamaskDisconnect(error) {
      console.log("Disconnection:", error);
    },

    handleEthereum() {
      const { ethereum } = window;
      if (ethereum && ethereum.isMetaMask) {
        alert("Ethereum successfully detected!");
        // Access the decentralized web!
      } else {
        alert("Please install MetaMask!");
      }
    },
  },
  created() {
    try {
      // Creating the dictionary for posting tags
      this.$store.dispatch("posts/setPostToTag", { root: true });
      // Handle axios errors
      axios.interceptors.response.use(
        (response) => {
          return response;
        },
        (error) => {
          if (error.response.status === 401) {
            //place your reentry code
            // this.$store.dispatch(
            //   "notification/add",
            //   { type: "error", message: "Your session was expired" },
            //   {
            //     root: true,
            //   }
            // );

            if (this.$store.state.user_auth_w2.isAuthentificated) {
              // If token is not removing, we will remove it ourselves
              this.$store.dispatch("user_auth_w2/removeToken", { root: true });
            }
            this.$router.push("/accounts/login");
          } else if (error.response.status === 403) {
            // Bad Role
            this.$store.dispatch(
              "notification/add",
              {
                type: "error",
                message: "You do not have permission to do that.",
              },
              {
                root: true,
              }
            );
            this.$router.push("/account");
            return null;
          }
          // return error;
          return Promise.reject(error);
        }
      );

      // Ether xD
      if (window.ethereum) {
        // Is on PC
        window.ethereum.on("accountsChanged", this.accountsChanged);
        window.ethereum.on("chainChanged", this.chainChanged);
      } else {
        // Possibly on mobile or some other provider overwriting
        this.$store.dispatch("user_account/logOutAccount", null, {
          root: true,
        });
        // window.location.replace("dapp:www.grifenix.com/");
        // window.location.href = "dapp:www.grifenix.com/";
      }
    } catch (e) {
      console.log("There was some Error:", e);
    }
  },
  mounted() {
    // Cheking the loggin coockies
    this.$store.dispatch("user_auth_w2/initializeStoreIfExists", {
      root: true,
    });
    // Initialize the user info
    // REVISAR ESTA PARTE
    if (this.$store.state.user_auth_w2.isAuthentificated) {
      this.$store.dispatch("account_data/getUserData", {
        root: true,
      });
      // Loading the user collections
      this.$store.dispatch("account_data/getUserServerCollections", {
        root: true,
      });
    }

    // Initialize the user wallet
    this.$store.dispatch("web3_wallet/onReEntered", { root: true });
  },
  unmounted() {
    if (window.ethereum) {
      // ON PC
      window.ethereum.removeListener("accountsChanged", this.accountsChanged);
      window.ethereum.removeListener("chainChanged", this.chainChanged);
      window.ethereum.removeListener("disconnect", this.metamaskDisconnect);
    } else {
      // On Mobile or some other provider overwriting
      console.log("Mobile unmounted");
    }
  },
  async getInfo() {
    let url = "https://gbk.grifenix.com/posts/";
    await axios.get(url).then((response) => {
      //console.log("Response:", response);
      this.data = response;
    });
  },
};
</script>

