<template>
  <div class="role_label">
    <div class="tag_style" :class="userRole">
      {{ this.capitalize(this.role.toLowerCase()) }}
    </div>
  </div>
</template>

<script>
export default {
  name: "gf-role-tag",
  props: {
    role: String,
  },
  data() {
    return {
      roleToSet: "-",
    };
  },
  methods: {
    capitalize(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    // formatUserRole(role) {},
  },
  computed: {
    userRole() {
      return `role_${this.role.trim()}`;
    },
  },
};
</script>

<style scoped>
.role_label {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0px 10px 0px 10px;
  display: flex;
  justify-content: center;
}
.tag_style {
  position: relative;
  width: fit-content;
  box-sizing: border-box;
  padding: 2px 10px;
  background-color: aqua;
  border-radius: 10px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  color: #16a000;
  margin: 5px 0px 5px 0px;
  z-index: 10;
}
.role_TESTER {
  background-color: rgb(240, 149, 0, 0.07) !important;
  color: #f09500 !important;
}
.role_USER {
  background-color: rgb(22, 160, 0, 0.07) !important;
  color: rgb(22, 160, 0, 1) !important;
}
.role_ORGANIZATION {
  background-color: rgb(0, 113, 218, 0.07) !important;
