    <div class="settings_page">
      <div class="title_container">
        <div class="title_text title_text_space">Account settings</div>
      </div>
      <div class="settings_containers">
        <div class="settings_container_enclosure">
          <div
            class="settings_container cc1"
            @click="this.$router.push('/account/password/update')"
          >
            <div class="title_text">Update account password</div>
          </div>
        </div>
        <div class="settings_container_enclosure">
          <div
            class="settings_container cc2"
            @click="this.$router.push('/account/information/update')"
          >
            <div class="title_text">Update account information</div>
          </div>
        </div>
        <div class="settings_container_enclosure">
          <div
            class="settings_container cc3"
            @click="
              cs();
              prueba();
            "
          >
            <div class="title_text">Billing dashboard</div>
          </div>
        </div>
        <div class="settings_container_enclosure">
          <div class="settings_container cc4" @click="cs()">
            <div class="title_text">Personal marketplace settings</div>
          </div>
        </div>
      </div>
      <div class="title_container">
        <div class="title_text title_text_space">Organization settings</div>
      </div>
      <div class="settings_containers">
        <div class="settings_container_enclosure">
          <div class="settings_container cc5" @click="cs()">
            <div class="title_text">Manage organization properties</div>
          </div>
        </div>
        <div class="settings_container_enclosure">
          <div class="settings_container cc6" @click="cs()">
            <div class="title_text">Manage accounts</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    prueba() {
      this.$store.dispatch("user_auth_w2/checkIfLogged", "/login", {
        root: true,
      });
    },
    cs() {
      const notification = {
        type: "information",
        message: "This implementation is coming soon!",
      };
      this.$store.dispatch("notification/add", notification, {
        root: true,
      });
    },
    scrollToTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    },
  },
  mounted() {
    this.scrollToTop();
  },
};
</script>

