<script>
import fut from "@/components/Footer";
import axios from "axios"; // To delete
export default {
  components: {
    fut,
  },
  data() {
    return {
      show_all: false,
      MAX_DESCRIPTION_LEN: 70,
      address_to_send: "",
      amount_to_send: 0.0,
      e_addr: false,
      msg_addr: false,
      closed_to_commissions: null,
    };
  },
  computed: {
    setImageBlur() {
      return `prof_img_blur_${this.$store.state.account_data.data.role}`;
    },
  },
  methods: {
    async getGPosts() {
      const url = "http://www.gbk.grifenix.com/hola/";
      const data = await axios.get(url);
      this.g_posts = data.data;
    },
    goto(link) {
      window.open(link, "_blank");
    },
    checkTransferInputs() {
      if (this.address_to_send.length > 0 || this.amount_to_send > 0) {
        this.msg_addr = true;
      } else {
        this.msg_addr = false;
      }
    },
    formatWalletAddress(addr) {
      let ad = "";
      try {
        let ini = addr.substring(0, 6);
        let end = addr.substring(addr.length - 4, addr.length);
        ad = ini + " *** " + end;
      } catch (e) {
        ad = "-";
      }
      return ad;
    },
    capitalize(string) {
      try {
        return string.charAt(0).toUpperCase() + string.slice(1);
      } catch (e) {
        return "-";
      }
    },
    cutDescription(desc) {
      try {
        if (desc.length > this.MAX_DESCRIPTION_LEN) {
          return desc.substring(0, this.MAX_DESCRIPTION_LEN) + "...";
        }
      } catch (e) {
        console.log("F");
      }
      return desc;
    },
    showAll() {
      if (this.show_all) {
        let text = this.cutDescription(
          this.capitalize(this.$store.state.account_data.data.description)
        );
        this.$refs.user_description.innerHTML = text;
      } else {
        let text = this.capitalize(
          this.$store.state.account_data.data.description
        );
        this.$refs.user_description.innerHTML = text;
      }
      this.show_all = !this.show_all;
    },
    loadUserData() {
      this.$store.dispatch("account_data/getUserData", {
        root: true,
      });
    },
  },
  mounted() {
    // this.loadUserData();
  },

  watch: {
    closed_to_commissions: function (newValue) {
      console.log("ValoR:", newValue);
    },
  },
};
</script>

