<template>
  <div class="tag_container" :class="getClass">
    {{ this.setTag() }}
  </div>
</template>

<script>
export default {
  name: "gf-yesno-tag",
  props: {
    option: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {};
  },
  methods: {
    setTag() {
      if (this.option) {
        return "YES";
      } else {
        return "NO";
      }
    },
  },
  computed: {
    getClass() {
      let sol = "no";
      if (this.option === true) {
        sol = "yes";
      }
      return `tag_option_${sol}`;
    },
  },
};
</script>

<style scoped>
.tag_container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 4px 10px;
  background: gray;
  border-radius: 10px;
  margin: 5px;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}
.tag_option_yes {
  background-color: rgb(22, 160, 0, 0.07);
  color: #16a000 !important;
}
.tag_option_no {
  background-color: rgb(187, 10, 33, 0.07);
  color: #bb0a21 !important;
}
</style>
