<template>
  <div class="profile_container">
    <gf-user-profile-menu :selectedOption="'My collections'" class="cont">
    </gf-user-profile-menu>
    <div class="all_container">
      <div class="central_container">
        <h1 class="subtitle_text">My collection dashboard</h1>
        <span class="generic_text space">
          This section contains the unuploaded collections and NFTS, as well as
          the Minted ones. Use this section to prepare your collection well
          before minting.
        </span>
        {{ checkRoleAndLogged }}
        <div class="toolbar">
          <div class="nft_scroller">
            <div class="creator_wrapper">
              <div class="creator_scroller">
                <div class="creator_content" ref="creator_content">
                  <div
                    class="creator_menu_item active"
                    @click="setOption('My collections')"
                  >
                    <span class="link_title"> My collections</span>
                    <span class="link_number">
                      {{ this.my_server_collections_number }}</span
                    >
                  </div>
                  <div class="creator_menu_item" @click="setOption('Created')">
                    <span class="link_title"> Created</span>
                    <span class="link_number">
                      {{ this.my_server_nfts_number }}</span
                    >
                  </div>
                  <div class="creator_menu_item" @click="setOption('Minted')">
                    <span class="link_title"> Minted </span>
                    <span class="link_number">
                      {{ this.creator_menu.owned }}</span
                    >
                  </div>
                  <div class="creator_menu_item" @click="setOption('On sale')">
                    <span class="link_title"> On sale</span>
                    <span class="link_number">
                      {{ this.creator_menu.posted }}</span
                    >
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="reload">
            <img
              @click="
                getUserNFTs();
                reloadCollections();
              "
              src="@/assets/icons/navbar/reload.svg"
              alt=""
            />
          </div>
          <div class="menu_filter" @click="this.toggleFilterState()">
            <img
              v-if="$store.state.navbar_state.dark_mode === true"
              src="@/assets/icons/navbar/filter_dark.svg"
              alt=""
            />
            <img v-else src="@/assets/icons/navbar/filter.svg" alt="" />
            <span v-if="this.filter_active === false">Show filter</span>
            <span v-if="this.filter_active === true">Quit filter</span>
          </div>
        </div>
        <div class="toolbar_filter" v-if="this.filter_active">
          <div class="tf_search">
            <gf-search-input v-model="valor"></gf-search-input>
          </div>
        </div>
        <!-- OWNED SECTION -->
        <div
          class="owned_section"
          v-if="this.creator_menu.selected_option === 'Minted'"
        >
          <div class="card_section">
            <div class="soon">
              <img src="@/assets/icons/soon.svg" alt="" />
              <span class="generic_text">
                The functionality will be added soon...
              </span>
            </div>
          </div>
        </div>
        <!-- ON SALE SECTION -->
        <div
          class="on_sale_section"
          v-if="this.creator_menu.selected_option === 'On sale'"
        >
          <div class="card_section">
            <div class="soon">
              <img src="@/assets/icons/soon.svg" alt="" />
              <span class="generic_text">
                The functionality will be added soon...
              </span>
            </div>
          </div>
        </div>
        <!-- CREATED SECTION -->
        <div
          class="created_section"
          v-if="this.creator_menu.selected_option === 'Created'"
        >
          <div class="card_section">
            <gf-nft-card
              v-for="(nft, key) in this.$store.state.account_data
                .user_server_nfts"
              :key="key"
              :nft_data="nft"
              :card_id="key"
            ></gf-nft-card>
          </div>
          <!-- <div
            class="card_section"
            v-for="(nft, key) in this.$store.state.account_data
              .user_server_nfts"
            :key="key"
          >
            {{ nft }}
          </div> -->
        </div>
        <!-- MY COLLECTIONS SECTION -->
        <div
          class="my_collection_section"
          v-if="this.creator_menu.selected_option === 'My collections'"
        >
          <div class="card_section">
            <gf-collection-card
              v-for="(collection, key) in this.$store.state.account_data
                .user_server_collections"
              :key="key"
              :nft_data="collection"
              :card_id="key"
            ></gf-collection-card>
          </div>
        </div>
      </div>
    </div>
  </div>
  <fut></fut>
</template>

<script>
import fut from "@/components/Footer";
export default {
  components: {
    fut,
  },
  data() {
    return {
      filter_active: false,
      creator_menu: {
        selected_option: "My collections",
        owned: 0,
        posted: 0,
        created: 0,
      },
      minted_nfts: false,
      nft_data: {
        owned_nfts: [
          {
            url: "https://lh3.googleusercontent.com/ROl89iB1Pi9JfqzEbByiOuWI4bChuA16097BJHdqMT8LRGwr1ZRYLUZogDhcYaoVVG40XUMpbIJp8_bhcYiFZjKB7o7qBsJynnDo=s400",
            status: "server",
          },
          {
            url: "https://lh3.googleusercontent.com/-f9DbwRCscMiEJRR7HdPUy23kR8BqGlaV7ICcnOjEnxX867-pcpL73IyYsEzTmYAQqiDFX7PmjenJavnTAUJLOzbWXnGr-cW757BOA=s1000",
            status: "blockchain",
          },
          {
            url: "https://assets.raribleuserdata.com/prod/v1/image/t_image_big/aHR0cHM6Ly9wcXBzLmZyYTEuZGlnaXRhbG9jZWFuc3BhY2VzLmNvbS9QbGFuZXRORlRzLzY3ZjZiMDdjYTcwNGIwZmM1ZDVlMWFlMjE4MmY0NWFjNmZjOTJkY2ZkN2NjNWY5MGQwNGZjMDgzOTk0YmEyNDAuanBn",
            status: "server",
          },
          {
            url: "https://lh3.googleusercontent.com/86mUviCcty4hLDmoFICCN0CnttQ0Eht-UuCYKgbhM7d_XYiRGB5sEUF6Ao5Y5lZFtIDbFz_iWAIjVfeVBm_AlmBtkLgKAm87U6pw=s1000",
            status: "server",
          },
        ],
        posted_nfts: [],
        created_nfts: [],
      },
    };
  },
  methods: {
    reloadCollections() {
      this.$store.dispatch("account_data/getUserServerCollections", {
        root: true,
      });
    },
    getUserNFTs() {
      this.$store.dispatch("account_data/getUserServerNFTs", {
        root: true,
      });
    },
    toggleFilterState() {
      this.filter_active = !this.filter_active;
    },
    formatWalletAddress(addr) {
      let ad = "";
      try {
        let ini = addr.substring(0, 6);
        let end = addr.substring(addr.length - 4, addr.length);
        ad = ini + " *** " + end;
      } catch (e) {
        ad = "-";
      }
      return ad;
    },
    capitalize(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    setOption(option) {
      let links = this.$refs.creator_content.children;
      for (let i = 0; i < links.length; i++) {
        links[i].classList.remove("active");
        // Adding to the actived link
        option = option.trim();
        // Adding active class
        let title = links[i].children[0].innerHTML.trim();
        if (title === option) {
          this.creator_menu.selected_option = option;
          links[i].classList.add("active");
        }
      }
    },
    scrollToTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    },
  },
  created() {
    if (this.$store.state.account_data.user_server_collections) {
      this.creator_menu.my_collections =
        this.$store.state.account_data.user_server_collections.length;
    } else {
      this.creator_menu.my_collections = 0;
    }
    this.scrollToTop();
  },
  computed: {
    my_server_collections_number() {
      return this.$store.state.account_data.user_server_collections.length;
    },
    my_server_nfts_number() {
      return this.$store.state.account_data.user_server_nfts.length;
    },
  },
};
</script>

