<template>
  <div class="register_container">
    <div class="register_form">
      <div class="logo_img">
        <img src="@/assets/icons/grifenix/gf_signup_blue.svg" alt="" />
      </div>
      <gf-input-field
        v-model="username"
        :text_type="'text'"
        :showError="e_username"
        :showRequired="true"
        :minSpace="true"
        @input="reset()"
      >
        <template v-slot:title>Username</template>
        <template v-slot:helper
          >Should be unique, you will use it to SignIn.</template
        >
      </gf-input-field>
      <gf-input-field
        v-model="email"
        :text_type="'text'"
        :showError="e_email"
        :showRequired="true"
        :minSpace="true"
        @input="reset()"
      >
        <template v-slot:title>Email</template>
        <template v-slot:helper>Will be used as login</template>
      </gf-input-field>
      <gf-input-field
        v-model="password"
        :text_type="'password'"
        :showError="e_password"
        :showRequired="true"
        :minSpace="true"
        @input="reset()"
      >
        <template v-slot:title>Password</template>
        <template v-slot:helper
          >Need A Stronger Password Before You Can Continue.</template
        >
      </gf-input-field>
      <gf-input-field
        v-model="password2"
        :text_type="'password'"
        :showError="e_password2"
        :showRequired="true"
        :minSpace="true"
        @input="reset()"
      >
        <template v-slot:title>Repeat your password</template>
        <template v-slot:helper
          >Need A Stronger Password Before You Can Continue.</template
        >
      </gf-input-field>
      <div class="terms_conds">
        By signing up, you agree to <span>our Terms</span> and
        <span>Privacy Policy</span>, as well as our use of cookies and similar
        technologies in <span>our Cookie Policy</span>.
      </div>
      <div class="cta">
        <!-- <gf-action-button
          class="login"
          @click="registerUser()"
          :label="'Create new account'"
        ></gf-action-button> -->
        <gf-action-button
          class="login inactive"
          :label="'Create new account'"
        ></gf-action-button>
      </div>
    </div>
    <div class="already_registered">
      <div class="terms_conds">
        You have an account?
        <span @click="this.$router.push('/accounts/login')">Sign In</span>.
      </div>
    </div>
  </div>
  <fut></fut>
</template>

<script>
import axios from "axios";
import fut from "@/components/Footer";
export default {
  components: {
    fut,
  },
  data() {
    return {
      username: "",
      email: "",
      password: "",
      password2: "",
      // Errors
      e_username: false,
      e_email: false,
      e_password: false,
      e_password2: false,
    };
  },
  methods: {
    scrollToTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    },
    reset() {
      (this.e_username = false), (this.e_email = false);
      this.e_password = false;
      this.e_password2 = false;
    },
    async registerUser() {
      const bodyData = new FormData();
      bodyData.append("username", this.username);
      bodyData.append("email", this.email);
      bodyData.append("password", this.password);
      bodyData.append("password2", this.password2);
      //const token = this.$store.state.user_auth_w2.token;
      // const url = this.$store.state.gf_contracts.backend;
      axios.defaults.headers.common["Authorization"] = ""; //"Token " + token;
      // await axios.post(url + "/register_tester/", bodyData).then((res) => {
      //   console.log("Data:", res);
      // });
    },
  },
  created() {
    this.scrollToTop();
  },
};
</script>

<style scoped>
