  font-size: 30px;
  line-height: 40px;
  color: #252525;
  font-weight: 600;
  margin: 0px 0px 0px 0px;
}
.inactive {
  opacity: 0.5;
}
.terms_conds {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #6e6e73;
  margin: 20px 0px 20px 0px;
}
.terms_conds span {
  box-sizing: border-box;
  padding: 0px 0px !important;
  text-decoration: none;
  color: #0071da;
  font-weight: 500;
  cursor: pointer;
}
.cta {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
}
.login {
  margin: 10px 0px 0px 0px;
}
.register_container {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: fit-content;
  min-height: 100vh;
  background-color: #f7f7f7;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 0px 10px 0px 10px;
}
.register_form,
.already_registered {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 450px;
  height: fit-content;
  background-color: white;
  box-sizing: border-box;
  justify-content: center;
  text-align: center;
  padding: 40px;
  margin: 100px 0px 0px 0px;
  border-radius: 16px;
  border: 1px solid #f2f2f2;
  -webkit-box-shadow: 5px 5px 10px 3px rgba(110, 110, 115, 0.03);
  -moz-box-shadow: 5px 5px 10px 3px rgba(110, 110, 115, 0.03);
  box-shadow: 5px 5px 10px 3px rgba(110, 110, 115, 0.03);
}
.already_registered {
  margin: 20px 0px 100px 0px;
}
.already_registered .terms_conds {
  margin: 0px;
}
.register_form > .container.form {
  margin: 10px 0px 0px 0px !important;
}
.logo_img {
  display: flex;
  justify-content: center;
  width: 100%;
  /* height: 130px; */
  box-sizing: border-box;
  padding: 10px 30px 30px 30px;
}
.logo_img img {
  width: 100%;
  max-width: 250px;
  height: auto;
  display: block;
  object-fit: contain;
}

@media (max-width: 467px) {
}
</style>
