<template>
  <div class="card_container_bg">
    <div class="card_container" :class="nftStatus">
      <div class="card_banner">
        <!-- <img
          :src="
            this.$store.state.gf_contracts.img_backend + this.nft_data.banner
          "
          alt=""
        /> -->
        <img
          src="https://rarible.mypinata.cloud/ipfs/bafybeiejc6ekvjzvscqacvansc3qq2fdxvkg4iah6zti2jiia2rbykxdb4"
          alt=""
        />
        <div class="statt label_text">
          {{ this.status }}
        </div>
      </div>
      <div class="coll_type">
        <span class="label_text">
          {{
            this.$store.state.gf_contracts.collection_types[
              this.nft_data.collection_type
            ]
          }}
        </span>
        <!-- <div class="collection_status label_text">
          {{ this.status }}
        </div> -->
      </div>
      <div class="subtitle_text">
        {{ this.capitalize(this.cutTitle(this.nft_data.name)) }}
      </div>
      <div class="options_nft">
        <div class="collection_items">
          <span class="generic_text">
            NFT items: <b>{{ getCollectionItemCount }}</b></span
          >
          <div>
            <span class="generic_text">Blockchain:</span>
            <img
              class="blockchain_img"
              src="@/assets/icons/blockchains/polygon.png"
              alt=""
            />
          </div>
        </div>
        <div class="thin_sep"></div>
        <div class="show_more">
          <span class="label_text_blue" @click="toggleInfoVisibility()"
            >More info</span
          >
        </div>
      </div>
      <div class="ctas">
        <gf-generic-button
          :label="'Create NFT'"
          @click="createNFTFor()"
        ></gf-generic-button>
        <gf-cta-button
          :label="'Check & mint'"
          @click="showCollection()"
        ></gf-cta-button>
      </div>

      <!-- MORE INFO -->
      <div class="more_info" :id="'more_info_' + this.card_id">
        <div class="info_wrapper">
          <div class="info_section">
            <div class="info_property">
              <div class="label_text">Collection name</div>
              <div class="generic_text">
                {{ this.capitalize(this.nft_data.name) }}
              </div>
            </div>
            <div class="info_property">
              <div class="label_text">Collection description</div>
              <div class="generic_text">
                {{ this.capitalize(this.nft_data.description) }}
              </div>
            </div>
            <div class="info_property">
              <div class="label_text">Payment token</div>
              <div class="generic_text">{{ this.nft_data.payment_token }}</div>
            </div>
            <div class="info_property">
              <div class="label_text">Creator fee</div>
              <div class="generic_text">{{ this.nft_data.creator_fee }}%</div>
            </div>
            <div class="info_property" v-if="extraFieldExist">
              <div class="label_text">Extra fields</div>
              <div class="info_extra_fields">
                <div
                  class="generic_text"
                  v-for="(field, fkey) in JSON.parse(
                    this.nft_data.extra_fields
                  )"
                  :key="fkey"
                >
                  <span>{{ this.capitalize(field.name) }} : </span>
                  <span>{{ field.value }}</span>
                </div>
              </div>
            </div>
            <div class="info_property" v-if="nft_data.social_links.length > 0">
              <div class="label_text">Social links</div>
              <div class="generic_text">
                <gf-social-tags
                  :social_networks="nft_data.social_links"
                  :show_creation="false"
                ></gf-social-tags>
              </div>
            </div>
            <div class="sep"></div>
            <div class="info_property">
              <div class="label_text">Collection type</div>
              <div class="generic_text">
                {{
                  this.$store.state.gf_contracts.collection_types[
                    this.nft_data.collection_type
                  ]
                }}
              </div>
            </div>
            <div class="info_property">
              <div class="label_text">Collection type description</div>
              <div class="generic_text">
                {{
                  this.$store.state.gf_contracts.collection_descriptions[
                    this.nft_data.collection_type
                  ]
                }}
              </div>
            </div>
          </div>
          <div class="btn_close">
            <div @click="toggleInfoVisibility()">
              <img src="@/assets/icons/navbar/go_arrow_left_dark.svg" alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "gf-collection-card",
  props: {
    card_id: Number,
    nft_data: Object,
    status: {
      type: String,
      default: "server",
    },
  },
  data() {
    return {
      nft_items: 0,
    };
  },
  methods: {
    capitalize(string) {
      let sol;
      try {
        sol = string.charAt(0).toUpperCase() + string.slice(1);
      } catch (e) {
        sol = "-";
      }
      return sol;
    },
    cutTitle(title) {
      try {
        if (title.length > 20) {
          return title.substring(0, 25) + "...";
        } else {
          return title;
        }
      } catch (e) {
        return "...";
      }
    },
    toggleInfoVisibility() {
      let infoCard = document.getElementById("more_info_" + this.card_id);
      if ([...infoCard.classList].includes("active")) {
        infoCard.classList.remove("active");
      } else {
        infoCard.classList.add("active");
      }
    },
    createNFTFor() {
      console.log("COLL:", this.nft_data);
      this.$store.state.account_data.nftForCollectionSelected =
        this.nft_data.id;
      // Redirect
      if (this.nft_data.collection_type === 0) {
        this.$router.push("/account/nft/create_simple_collection");
      } else if (this.nft_data.collection_type === 1) {
        this.$router.push("/account/nft/create_simple_collection_utility");
      } else {
        this.$router.push("/account/nft/dashboard");
      }
    },
    showCollection() {
      this.$store.state.account_data.nftForCollectionSelected =
        this.nft_data.id;
      // Redirect
      if (this.nft_data.collection_type === 0) {
        this.$router.push("/account/marketplace/simple_collection");
      } else if (this.nft_data.collection_type === 1) {
        this.$router.push("/account/nft/dashboard");
      } else {
        this.$router.push("/account/nft/dashboard");
      }
    },
  },
  computed: {
    nftStatus() {
      return `nft_status_${this.status.trim()}`;
    },
    extraFieldExist() {
      let res = false;
      try {
        if (JSON.parse(this.nft_data.extra_fields)[0].name) {
          return true;
        }
      } catch (e) {
        return res;
      }
      return res;
    },
    getCollectionItemCount() {
      return this.$store.state.account_data.user_collection_to_nft_ids[
        this.nft_data.id
      ]
        ? this.$store.state.account_data.user_collection_to_nft_ids[
            this.nft_data.id
          ].length
        : 0;
    },
  },
};
</script>

<style scoped>
.label_text_blue {
  color: #0071da;
  font-size: 16px;
  font-weight: 500;
  margin: 0px 0px 0px 5px;
  cursor: pointer;
}
.label_text {
  color: #6e6e73;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
}
.generic_text {
  color: #6e6e73;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
}
.subtitle_text {
  color: #252525;
  font-size: 18px;
  line-height: 24px;
  font-weight: 600;
}
.card_container_bg {
  position: relative;
  width: fit-content;
  height: fit-content;
  box-sizing: border-box;
  padding: 5px 0px 0px 0px;
}
.blockchain_img {
  height: 25px;
  width: 25px;
}
.card_container {
  position: relative;
  height: fit-content;
  width: fit-content;
  min-width: 350px;
  background-color: white;
  border: 1px solid #ebe9e9 !important;
  -webkit-box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.03);
  box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.03);
  transition-duration: 0.3s;
  cursor: pointer;
  padding: 20px 20px 30px 20px;
  overflow: hidden;
}
.card_banner {
  position: relative;
  height: fit-content;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
  border-radius: 10px;
  margin: 0px 0px 15px 0px;
}
.statt {
  position: relative;
  display: flex;
  height: fit-content;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: rgb(0, 113, 218, 0.2) !important;
  color: rgb(0, 113, 218);
}
.card_banner img {
  position: relative;
  width: 100%;
  height: auto;
  min-height: 100px;
  max-height: 100px;
  object-fit: cover;
  background-color: #ebe9e9;
}
.coll_type {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  margin: 20px 0px 0px 0px;
}
.card_container .subtitle_text {
  margin: 10px 0px 20px 0px;
}
.collection_status {
  position: relative;
  box-sizing: border-box;
  padding: 2px 10px;
  height: fit-content;
  width: fit-content;
  border-radius: 10px;
  margin: 0px 0px 0px 10px;
}
.nft_status_server .collection_status {
  background-color: rgb(0, 113, 218, 0.2) !important;
  color: rgb(0, 113, 218);
}
.nft_status_blockchain .collection_status {
  background-color: rgb(22, 160, 0, 0.2) !important;
  color: rgb(22, 160, 0);
}

.collection_items {
  position: relative;
  height: fit-content;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0px 0px 10px 0px;
}
.collection_items div {
  position: relative;
  display: flex;
  flex-direction: row;
  width: fit-content;
  height: 25px;
  width: fit-content;
}
.collection_items div img {
  position: relative;
  height: 100%;
  width: 100%;
  margin: 0px 0px 0px 5px;
}
.show_more {
  position: relative;
  width: 100%;
  height: fit-content;
  display: flex;
  justify-content: center;
  text-align: center;
  margin: 0px 0px 0px 0px;
}
.options_nft {
  position: relative;
  display: flex;
  height: fit-content;
  flex-direction: column;
  width: 100%;
  border: 1px solid #ebe9e9;
  border-radius: 16px;
  box-sizing: border-box;
  padding: 25px 15px 15px 15px;
  margin: 15px 0px 0px 0px !important;
}
.ctas {
  position: relative;
  height: fit-content;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 30px 0px 0px 0px !important;
  gap: 10px;
}

/* More Info */
.more_info {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1000;
  top: 0px;
  left: 0px;
  box-sizing: border-box;
  padding: 5px;
  border-radius: 16px;
  transform: translateY(-110%);
  transition-duration: 0.3s;
  opacity: 0;
}
.active.more_info {
  transform: translateY(0%);
  opacity: 1;
  transition-duration: 0.3s;
}
.info_wrapper {
  position: relative;
  min-height: 100%;
  width: 100%;
  background-color: white;
  height: -moz-fit-content;
  height: fit-content;
  overflow-x: hidden;
  overflow-y: auto;
  box-sizing: border-box;
}
.info_wrapper::-webkit-scrollbar {
  width: 10px;
  background: white;
  cursor: pointer !important;
  border-radius: 0px 10px 10px 0px;
  border-left: 1px solid #f2f2f2;
}
.info_wrapper::-webkit-scrollbar-thumb {
  background: #6e6e75;
  border-radius: 0px 10px 10px 0px;
  cursor: pointer !important;
}
.info_section {
  position: absolute;
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
  box-sizing: border-box;
  padding: 30px 30px 60px 30px;
}
.btn_close {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 160;
  height: 40px;
  width: 100%;
  bottom: 10px;
  left: 0px;
}
.btn_close div {
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0071da;
  border-radius: 50%;
}
.btn_close div img {
  height: 25px;
  width: 25px;
  transform: rotate(90deg);
}
.info_property {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: left !important;
  justify-content: flex-start !important;
  height: -moz-fit-content;
  height: fit-content;
  box-sizing: border-box;
  padding: 15px 0px 0px 0px;
}
.info_extra_fields div span {
  color: #6e6e73 !important;
}
.info_extra_fields .generic_text {
  position: relative;
  box-sizing: border-box;
  padding: 0px 0px 0px 10px;
}
.info_extra_fields .generic_text::before {
  position: absolute;
  content: "";
  left: 0px;
  top: 9px;
  height: 5px;
  width: 5px;
  border-radius: 50%;
  background-color: #252525;
}
@media (max-width: 765px) {
  .card_container {
    min-width: 0px !important;
    width: 100%;
  }
  .ctas {
    flex-direction: column-reverse;
  }
}
@media (max-width: 465px) {
  .collection_items {
    flex-direction: column;
    row-gap: 15px;
  }
  .card_container_bg {
    width: 100%;
  }
  .card_container {
    min-width: none;
    width: 100% !important;
    margin: 10px 0px 15px 0px;
    max-width: 100%;
  }
}
@media (hover: hover) {
  .card_container:hover {
    -webkit-box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.07);
    box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.07);
    transition-duration: 0.3s;
  }
}
</style>
