<template>
  <div class="profile_container">
    <gf-user-profile-menu :selectedOption="'NFT builder'" class="cont">
    </gf-user-profile-menu>
    <div class="all_container">
      <div class="central_container">
        <h1 class="subtitle_text">NFT builder</h1>
        <div class="toolbar_filter">
          <gf-search-input
            v-model="search_value"
            :shadow="false"
            :border="true"
            :margin="false"
            :border_right="false"
            :max_width="true"
            class="search_input"
          ></gf-search-input>
          <div class="favorites_text">
            <gf-checkbox v-model="favorites" :shadow="false"></gf-checkbox>
            <span class="generic_text">Favorites</span>
          </div>
        </div>
        <div class="templates_container">
          <div
            class="template_container"
            v-for="(template, key) in filteredTemplates"
            :key="key"
          >
            <div class="template_data">
              <div class="favorite_container">
                <div
                  class="favorite_btn"
                  @click="changeFavoriteTematics(template.id)"
                >
                  <img
                    v-if="favoriteTemplates.includes(template.id)"
                    src="@/assets/icons/generic/favorite_active.svg"
                    alt=""
                  />
                  <img
                    v-else
                    src="@/assets/icons/generic/favorite_inactive.svg"
                    alt=""
                  />
                </div>
              </div>
              <div class="template_img">
                <img :src="require('./../../' + template.icon)" alt="" />
              </div>
              <h1 class="subtitle_text">{{ template.title }}</h1>
              <h2 class="generic_text">{{ template.description }}</h2>
            </div>
            <div class="ctas">
              <gf-generic-button
                @click="toggleInfoVisibility(key)"
                :label="'More info'"
              ></gf-generic-button>
              <gf-cta-button
                @click="this.$router.push(template.link)"
                :label="'Create Collection'"
              ></gf-cta-button>
            </div>
            <!-- More info -->
            <div class="more_info" :id="'more_info_' + key">
              <div class="info_wrapper">
                <div class="info_section">
                  <div class="info_property">
                    <h1 class="label_text">Collection name</h1>
                    <h2 class="generic_text">{{ template.title }}</h2>
                  </div>
                  <div class="info_property">
                    <h1 class="label_text">Use case example</h1>
                    <h2 class="generic_text">{{ template.use_case }}</h2>
                  </div>
                  <div class="thin_sep"></div>
                  <div class="info_property">
                    <h1 class="label_text">On-chain data</h1>
                    <div v-for="(item, k) in template.onchain_data" :key="k">
                      <h2 class="generic_text item">{{ item }}</h2>
                    </div>
                  </div>
                </div>
                <div class="btn_close">
                  <div @click="toggleInfoVisibility(key)">
                    <img
                      src="@/assets/icons/navbar/go_arrow_left_dark.svg"
                      alt=""
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <fut></fut>
</template>

<script>
import fut from "@/components/Footer";
export default {
  components: {
    fut,
  },
  data() {
    return {
      search_value: "",
      favorites: false,
      user_templates: [
        {
          id: 0,
          icon: "assets/icons/collections/simple_collection.png",
          title: "Simple collection",
          favorite: true,
          description:
            "An ERC-721 contract which contains a collections of inmutable NFTs, once the collection is minted, you will not be able to edit its properties.",
          use_case: "xD",
          onchain_data: [
            "Title.",
            "Description.",
            "Price",
            "Image Link/Bytecode.",
          ],
          link: "/account/collection/create_simple_collection",
        },
        {
          id: 1,
          icon: "assets/icons/collections/simple_collection_utility.png",
          title: "SImple collection + utility",
          favorite: false,
          description:
            "Same as a 'Simple collection' but you can also add a utility contract which will handle some unique utilities you want and can edit later.",
          use_case: "F",
          onchain_data: [
            "Title.",
            "Description.",
            "Price",
            "Image Link/Bytecode.",
            "Utility contract addresses which you can add later.",
          ],
          link: "/account/collection/create_simple_collection_utility",
        },
      ],
    };
  },
  methods: {
    scrollToTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    },
    capitalize(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    setOption(option) {
      let links = this.$refs.creator_content.children;
      for (let i = 0; i < links.length; i++) {
        links[i].classList.remove("active");
        // Adding to the actived link
        option = option.trim();
        // Adding active class
        let title = links[i].children[0].innerHTML.trim();
        if (title === option) {
          this.creator_menu.selected_option = option;
          links[i].classList.add("active");
        }
      }
    },
    toggleInfoVisibility(id) {
      let infoCard = document.getElementById("more_info_" + id);
      console.log(infoCard);
      if ([...infoCard.classList].includes("active")) {
        infoCard.classList.remove("active");
      } else {
        infoCard.classList.add("active");
      }
    },
    changeFavoriteTematics(id) {
      this.$store.dispatch("account_data/changeFavoriteTemplates", id, {
        root: true,
      });
    },
  },
  computed: {
    favoriteTemplates: function () {
      const favs = this.$store.state.account_data.data.favorite_templates;
      console.log("Favs:", favs);
      return favs;
    },
    filteredTemplates: function () {
      return this.user_templates.filter((temp) => {
        // Favorites filter
        let isGood = false;
        let favorites = this.$store.state.account_data.data.favorite_templates;
        if (this.favorites && favorites && favorites.length > 0) {
          if (favorites.includes(temp.id)) {
            return true;
          } else {
            return false;
          }
        }

        let search = this.search_value.toLowerCase();
        if (
          temp.title.toLowerCase().match(search) ||
          temp.description.toLowerCase().match(search)
        ) {
          isGood = isGood || true;
        }
        return isGood;
      });
    },
  },
  watch: {
    filtered: function () {
      this.filteredTemplates;
    },
  },
  created() {
    // this.$store.dispatch("account_data/getUserServerCollections", {
    //   root: true,
    // });
    this.scrollToTop();
  },
};
</script>

<style scoped>
.generic_text {
  color: #6e6e73;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
}
.label_text {
  color: #6e6e73;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
}
.label_text_blue {
  color: #0071da;
  font-size: 16px;
  font-weight: 500;
  margin: 0px 0px 0px 5px;
  cursor: pointer;
}
.active_link {
  color: rgb(0, 113, 218) !important;
  border: 2px solid rgb(0, 113, 218);
}
.subtitle_text {
  color: #252525;
  font-size: 18px;
  line-height: 27px;
  font-weight: 600;
}
.profile_container {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: fit-content;
  min-height: 100vh;
  /* background-color: #ebe9e9; */
}
.all_container {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: fit-content;
  min-height: 100vh;
  /* background-color: #ebe9e9; */
}
.central_container {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 110vh;
  width: 100%;
  /* background: #f7f7f7; */
  background-color: #fefefe;
  box-sizing: border-box;
  padding: 120px 30px 100px 30px;
}
.toolbar_filter {
  position: relative;
  width: 100%;
  display: flex;
  height: fit-content;
  flex-direction: row;
  border: 1px solid #ebe9e9 !important;
  -webkit-box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.03);
  box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.03);
  margin: 15px 0px 30px 0px;
  border-radius: 16px;
  flex-wrap: wrap;
  box-sizing: border-box;
  padding: 0px 0px 0px 0px;
  background-color: white;
  justify-content: left;
  align-items: center;
  overflow: hidden;
  transition-duration: 0.3s;
}

.checkbox_items {
  position: relative;
  display: flex;
  flex-direction: row;
  width: fit-content;
  height: fit-content;
  box-sizing: border-box;
  padding: 5px 15px;
}
.checkbox_item {
  position: relative;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  background: #ffffff;
  outline: none;
  padding: 0;
  margin: 0;
  opacity: 0;
}
.ctas {
  position: relative;
