  z-index: 500;
}
.favorite_btn img {
  pointer-events: inherit;
  position: relative;
  height: 100%;
  width: 100%;
}
/* Templates */
.templates_container {
  position: relative;
  width: 100%;
  height: fit-content;
  min-height: 100px;
  display: flex;
  flex-direction: row;
  row-gap: 15px;
  column-gap: 15px;
  flex-wrap: wrap;
  justify-content: space-around;
  /* background-color: #6e6e73; */
