}
.template_container {
  position: relative;
  display: flex;
  /* flex-shrink: 0;
  flex-grow: 1; */
  width: 100%;
  max-width: 400px;
  height: fit-content;
  min-height: 300px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  border-radius: 16px;
  border: 1px solid #ebe9e9 !important;
  box-shadow: 0px 0px 15px 5px rgb(37 37 37 / 3%);
  box-sizing: border-box;
  padding: 30px 20px;
  cursor: pointer;
  transition-duration: 0.3s;
  overflow: hidden;
}
.template_data {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  margin: 0px 0px 30px 0px;
}
.template_container h1 {
  margin: 15px 0px 15px 0px;
}
.template_container h2 {
  text-align: center;
  height: auto;
  justify-content: stretch;
}
.template_img {
  position: relative;
  width: 100%;
  height: fit-content;
  display: flex;
  justify-content: center;
  margin: 15px 0px 15px 0px;
}
.template_img img {
  position: relative;
  height: 80px;
  width: 80px;
}
.more_info {
  position: absolute;
  box-sizing: border-box;
  padding: 5px;
  border-radius: 16px;
  left: 0px;
  width: 100%;
  height: 100%;
  top: 0px;
  transform: translateY(-110%);
  transition-duration: 0.3s;
  /* border: 1px solid #0071da; */
  /* border: 1px solid #ebe9e9 !important; */
  opacity: 0;
}
.active.more_info {
  transform: translateY(0%);
  opacity: 1;
  transition-duration: 0.3s;
}
.info_wrapper {
  position: relative;
  min-height: 100%;
  width: 100%;
  background-color: white;
  height: fit-content;
  overflow-x: hidden;
  overflow-y: auto;
  box-sizing: border-box;
}
.info_wrapper::-webkit-scrollbar {
  width: 10px;
  background: white;
  cursor: pointer !important;
  border-radius: 0px 10px 10px 0px;
  border-left: 1px solid #f2f2f2;
}
.info_wrapper::-webkit-scrollbar-thumb {
  background: #6e6e75;
  border-radius: 0px 10px 10px 0px;
  cursor: pointer !important;
}
.info_section {
  position: absolute;
  width: 100%;
  height: fit-content;
  box-sizing: border-box;
  padding: 30px 30px 60px 30px;
}
.info_property {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: left !important;
  justify-content: flex-start !important;
  height: fit-content;
  box-sizing: border-box;
  padding: 15px 0px 0px 0px;
}
.info_property .item {
  position: relative;
  box-sizing: border-box;
  padding: 0px 0px 0px 10px;
}
.info_property .item::before {
  position: absolute;
  left: 0px;
  top: 9px;
  content: "";
  background-color: #6e6e73;
  border-radius: 50%;
  height: 5px;
  width: 5px;
}
.info_property h1,
.info_property h2 {
  text-align: left !important;
  justify-content: flex-start !important;
  margin: 0px;
}
.btn_close {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 160;
  height: 40px;
  width: 100%;
  bottom: 10px;
  left: 0px;
}
.btn_close div {
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0071da;
  border-radius: 50%;
}
.btn_close div img {
  height: 25px;
  width: 25px;
  transform: rotate(90deg);
}
@media (max-width: 1300px) {
  .all_container {
    flex-direction: column-reverse;
  }
  .central_container {
    padding: 100px 30px 10px 30px;
  }
}
@media (max-width: 800px) {
  .central_container {
    min-width: 0px;
  }
  .search_input {
    max-width: 100% !important;
    margin: 0px 0px 0px 0px;
  }
  .favorites_text {
    margin: 15px 15px 0px 15px;
  }
  .toolbar_filter {
    padding: 0px 0px 15px 0px;
    transition-duration: 0.3s;
  }
}
@media (max-width: 764px) {
}
@media (max-width: 476px) {
}
@media (hover: hover) {
  .template_container:hover {
    -webkit-box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.07);
    box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.07);
    transition-duration: 0.3s;
  }
  .favorite_btn:hover {
    padding: 0px 0px 3px 0px;
    transition-duration: 0.3s;
  }
}
</style>
