<template>
  <div class="container">
    <div class="label_container">
      <div class="wallet_ico">
        <img src="@/assets/wallet.svg" alt="" />
      </div>
      <div>
        {{ getCurrentWalletAddr }}
      </div>
      <div class="vertical_sep"></div>
      <div>
        {{ getCurrentWalletBalance }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "gf-user-connected-label",
  data() {
    return {};
  },
  methods: {},
  computed: {
    getCurrentWalletAddr() {
      const wallet = this.$store.state.web3_wallet.web3_account.wallet_address;
      return wallet.slice(0, 5) + "..." + wallet.slice(-3);
    },
    getCurrentWalletBalance() {
      const balance = (
        this.$store.state.web3_wallet.web3_account.balance /
        10 ** 18
      ).toFixed(2);
      const symbol =
        this.$store.state.web3_wallet.symbol[
          this.$store.state.web3_wallet.web3_account.network.chainId
        ] || "-";
      return balance + " " + symbol;
    },
  },
};
</script>

<style scoped>
.container {
  position: relative;
  height: fit-content;
  width: fit-content;
}
.vertical_sep {
  position: relative;
  height: 100%;
  width: 1px;
  background-color: #0071da;
  margin: 0px 5px 0px 5px;
}
.wallet_ico {
  position: relative;
  margin: 0px 5px 0px 0px;
  box-sizing: border-box;
  padding: 0px 0px 0px 0px;
  height: 100%;
  width: 100%;
  max-width: 25px;
}
.wallet_ico img {
  position: relative;
  height: 100%;
  width: 100%;
}
.label_container {
  position: relative;
  height: 25px;
  width: fit-content;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  padding: 3px 10px 3px 4px;
  background-color: rgb(0, 113, 218, 0.1);
  color: #0071da;
  border-radius: 16px;
  font-size: 14px;
  white-space: nowrap;
}
</style>
