import { createStore } from 'vuex'

// Notifications
import * as notification from '@/store/modules/notification.js'

// Material downloaders
import * as posts from '@/store/modules/posts.js'
import * as digests from '@/store/modules/digests.js'

// The rest
import * as user_account from '@/store/modules/user_account.js'
import * as constants from '@/store/modules/constants.js'
import * as gf_contracts from '@/store/modules/gf_contracts.js'
import * as user_auth_w2 from '@/store/modules/user_auth_w2.js'
import * as navbar_state from '@/store/modules/navbar_state.js'
import * as account_data from '@/store/modules/account_data.js'
import * as web3_wallet from '@/store/modules/web3_wallet.js'
import * as helpers from '@/store/modules/helpers.js'
import * as contract_abis from '@/store/modules/contract_abis.js'
export default createStore({
    state: {
        networks: {
            1: "mainnet",
            3: "ropsten",
            4: "rinkeby",
            42: "kovan",
            5: "goerli",
            80001: "mumbai"
        }
    },
    mutations: {},
    actions: {},
    getters: {
        isEthereum() {
            if (window.ethereum) {
                return true;
            } else {
                return false;
            }
        },
        currentAccount() {
            if (window.ethereum) {
                return window.ethereum.selectedAddress;
            } else {
                return null;
            }
        },

    },
    modules: {
        notification,
        posts,
        digests,
        user_account,

        gf_contracts, // Some constants and all the related to server 
        user_auth_w2, // All related to the authentification methods
        navbar_state, // All related to the navbar states
        account_data, // The account data stored on the PC
        constants, // All varibles relates to functionality
        web3_wallet, // Handles all the infor of the wallet for web3
        helpers, // Helper functions
        contract_abis, // Contains the abis of all our contracts
    }
})