<template>
  <div class="checkbox_container">
    <input
      :class="{ shadows: shadow }"
      class="input_terms"
      type="checkbox"
      name="toggle"
      :id="id"
      :checked="check_opc"
      @change="emitChange($event)"
    />
    <label for="id" class="input_terms_label"
      ><div v-if="check_opc" class="check">✓</div></label
    >
  </div>
</template>

<script>
export default {
  name: "gf-checkbox",
  props: {
    checked: {
      type: Boolean,
      default: false,
    },
    shadow: {
      type: Boolean,
      defaut: true,
    },
  },
  data() {
    return {
      id: null,
      check_opc: false,
    };
  },
  methods: {
    emitChange(ev) {
      this.check_opc = ev.target.checked;
      this.$emit("update:modelValue", ev.target.checked);
    },
  },
  mounted() {
    this.check_opc = this.checked;
    this.id = this._uid;
  },
};
</script>

<style scoped>
.input_terms {
  position: relative;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  background: #ffffff;
  outline: none;
  padding: 0;
  margin: 0;
  opacity: 0;
  cursor: pointer;
}
.input_terms_label {
  display: flex;
  align-content: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  background: none;
  border: 2px solid #ebe9e9; /*#f2f2f2;*/
  border-radius: 10px;
  pointer-events: none;
}
.input_terms:focus ~ .input_terms_label {
  background: white;
  border: 1px solid #f2f2f2;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
.shadows.input_terms:focus ~ .input_terms_label {
  -webkit-box-shadow: 0px 0px 25px 10px rgba(37, 37, 37, 0.07);
  box-shadow: 0px 0px 25px 10px rgba(37, 37, 37, 0.07);
}
.check {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #76b041;
  font-weight: 600;
}

