<template>
  <div class="container">
    <div class="sep"></div>

    <div class="btn" @click="sendData()">Send Data</div>
    <div class="sep"></div>

    <div class="btn" @click="createCollection()">Create new collection</div>
    <div class="sep"></div>
    <div class="btn" @click="deployDummyContract()">Deploy dummy contract</div>
    <div class="sep"></div>
    <gf-image-drop-file
      v-model="image_to_upload"
      :defaultURL="image_to_upload"
    ></gf-image-drop-file>

    {{ image_to_upload }}
    <div class="btn" @click="getImage()">getImage</div>
    <div
      class="btn"
      @click="
        getImageFromURL(
          'https://grifenixs3.s3.amazonaws.com/news_blog_media/Admin/eacaefe9-ab09-49cc-8370-3d4cfc5af947.Frame_21_2.png'
        )
      "
    >
      getImageFromURL
    </div>
    <div class="sep"></div>
    <div>Address: {{ this.address }}</div>
    <div>Number: {{ this.number }}</div>
    <div class="btn" @click="connectWallet()">Connect wallet</div>
    <div class="btn" @click="addNumber()">addNumber</div>
    <div class="btn" @click="getNumber()">getNumber</div>
    <div class="sep"></div>
    <div>
      Network: {{ this.$store.state.web3_wallet.web3_account.network.name }}
    </div>
    <div>Balance: {{ this.$store.state.web3_wallet.web3_account.balance }}</div>
    <div>
      Address: {{ this.$store.state.web3_wallet.web3_account.wallet_address }}
    </div>
    <div class="btn" @click="accountsChanged()">Accounts Changed</div>
    <div class="btn" @click="connectMetaMask()">Connect wallet</div>
    <div class="btn" @click="disconnectMetaMask()">Disconnect wallet</div>
  </div>
</template>

