  methods: {
    async sendData() {
      const token = this.$store.state.user_auth_w2.token;
      axios.defaults.headers.common["Authorization"] = "Token " + token;
      const url = "http://192.168.0.21:8000/post_gdb_collection";
      await axios.post(url).then((res) => {
        console.log("Response: ", res.data);
      });
    },
    async deployDummyContract() {
      const ethers = require("ethers");
      this.ethers = ethers;
      if (typeof window.ethereum !== "undefined") {
        this.provider = new this.ethers.providers.Web3Provider(
          window.ethereum,
          "any"
        );
        await this.provider.send("eth_requestAccounts", []).then((res) => {
          this.address = res[0];
        });

        // try {
        const signer = this.provider.getSigner(0);
        const factory = new this.ethers.ContractFactory(
          this.abi3,
          this.bytecode3,
          signer
        );
        const contract = await factory.deploy(this.address, "hola");
        console.log("contract:", contract.address);
      }
    },
    async createCollection() {
      const ethers = require("ethers");
      this.ethers = ethers;
      if (typeof window.ethereum !== "undefined") {
        this.provider = new this.ethers.providers.Web3Provider(
          window.ethereum,
          "any"
        );
        await this.provider.send("eth_requestAccounts", []).then((res) => {
          this.address = res[0];
        });

        // try {
        const signer = this.provider.getSigner(0);

        const contract = new this.ethers.Contract(
          "0xDaCa8549866e26aAB99C1128158843Bd2B47c50c",
          this.abi2,
          signer
        );
        await contract.createContract("a", "b", 1, "description", "links", {
          gasLimit: 2000000,
        });
        // } catch (e) {
        //   console.log("UPS");
        // }
      }
    },
    async getImage() {
      console.log("Image:", this.image_to_upload);

      const url =
        "https://grifenixs3.s3.amazonaws.com/news_blog_media/Admin/eacaefe9-ab09-49cc-8370-3d4cfc5af947.Frame_21_2.png";

      fetch(url).then(async (response) => {
        const contentType = response.headers.get("content-type");
        const blob = await response.blob();

        const file = new File([blob], "img.png", { contentType });
        // access file here
        if (blob) {
          this.image_to_upload = file;
        }
        // this.image_to_upload = blob;
      });
    },
    async getImageFromURL(url) {
      fetch(url).then(async (response) => {
        const contentType = response.headers.get("content-type");
        const blob = await response.blob();
        // Allowed extension
        let item = this.$store.state.helpers.TYPE_TO_EXTENSION[contentType];
        if (typeof item === "undefined") {
          // Type not allowed
          const notification = {
            type: "error",
            message: "The given type is not allowed",
          };
          this.$store.dispatch("notification/add", notification, {
            root: true,
          });
          return null;
        }
        // Next file transformations
        const file = new File([blob], "img" + item, { contentType });
        if (!file) {
          console.log("no file");
          return null; // There is no file
        }
        // Too large size
        if (file.size > this.$store.state.helpers.MAX_FILE_SIZE) {
          // Allowed extension
          const msg = `File is too large. Max size is ${
            this.$store.state.helpers.MAX_FILE_SIZE / 1000000
          }MB`;
          const notification = {
            type: "error",
            message: msg,
          };
          this.$store.dispatch("notification/add", notification, {
            root: true,
          });
        }
        // access file here
        if (blob) {
          this.image_to_upload = file;
        } else {
          this.image_to_upload = null;
        }
        // this.image_to_upload = blob;
      });
    },

    async connectWallet() {
      const ethers = require("ethers");
      this.ethers = ethers;
      if (typeof window.ethereum !== "undefined") {
        this.provider = new this.ethers.providers.Web3Provider(
          window.ethereum,
          "any"
        );
        await this.provider.send("eth_requestAccounts", []).then((res) => {
          this.address = res[0];
        });
      }
    },

    async addNumber() {
      try {
        const signer = this.provider.getSigner(0);

        const contract = new this.ethers.Contract(
          "0x08b9cd494e899E170c59d1cEb23Ab401A873e02b", //this.contractAddress,
          this.abi3,
          signer
        );
        await contract.setData("que tal");
        //await contract.addNumber(1);
      } catch (e) {
        console.log("UPS");
      }
    },

    async getNumber() {
      try {
        const signer = this.provider.getSigner(0);

        const contract = new this.ethers.Contract(
          this.contractAddress,
          this.abi,
          signer
        );
        this.number = await contract.getNumber();
      } catch (e) {
        console.log("UPS");
      }
    },

    async accountsChanged() {
      this.$store.dispatch("web3_wallet/metamaskAccountsChanged", {
        root: true,
      });
    },
    async connectMetaMask() {
      this.$store.dispatch("web3_wallet/connectMetaMask", {
        root: true,
      });
    },
    async disconnectMetaMask() {
      this.$store.dispatch("web3_wallet/logoutWallet", {
        root: true,
      });
    },
  },
