export const namespaced = true

export const state = {
    ALLOWED_NETWORK_IDS: [80001], // If we will start in mumbai, we will only allow the mumbai netorks
    ethers: null, // Contains all the ethers functional
    wallet_connected: false, // When connected, will be set to True
    web3_account: {
        wallet_address: "", // Address of the wallet
        balance: 0.0, // Wallet balance
        network: "", // Network which must be very controlled
        provider: null, // Metamask or other wallets provider
        wallet_used: "metamask", // Wallet used to connect Web3 
    },
    networks: {
        1: "mainnet",
        3: "ropsten",
        4: "rinkeby",
        42: "kovan",
        5: "goerli",
        80001: "mumbai",
        11155111: "sepolia"
    },
    symbol: {
        1: "ETH",
        3: "ETH",
        4: "ETH",
        42: "ETH",
        5: "ETH",
        11155111: "ETH",
        137: "MATIC",
        80001: "MATIC",
    },
}

export const getters = {
    // -> getters['posts/getX']
    getWalletStorage() {
        const wallet_storage = JSON.parse(window.localStorage.getItem("gf_account_wallet"));
        if (wallet_storage) {
            return wallet_storage[0]
        } else {
            return null
        }
    },
    isWalletConnected(state) {
        return state.wallet_connected;
    },
    isWalletStorage() {
        if (
            typeof window.localStorage.getItem("gf_account_wallet") === "undefined"
        ) {
            return false;
        } else {
            return true;
        }
    },
}

export const mutations = {
    // -> commit('posts/mutationX') Synchronous

}

export const actions = {
    // -> dispatch('posts/actionX') Asynchronous

    // async changeFavoriteTemplates({ state, rootState }, templateID) {

    // }

    // ===================================
    // Generic functions 
    // ===================================
    async createWalletStorage() {
        const account = [{
            network: state.web3_account.network,
            wallet: state.web3_account.wallet_address,
            wallet_used: state.web3_account.wallet_used,
        }]
        window.localStorage.setItem("gf_account_wallet", JSON.stringify(account));
    },
    async deleteWalletStorage() {
        // We just should delete the storage with account info
        window.localStorage.removeItem("gf_account_wallet");
    },
    // Gets the provider balance depending on the wallet type
    async getBalance({ state }, addr) {
        if (state.web3_account.wallet_used === "metamask" && state.web3_account.provider) {
            state.web3_account.provider.getBalance(addr).then((balance) => {
                state.web3_account.balance = Number(balance) || 0.0
            })
        } else {
            state.web3_account.balance = 0.0
        }
    },
    // gets the network depending on the wallet type
    async getNetwork({ state }) {
        if (state.web3_account.wallet_used === "metamask" && state.web3_account.provider) {
            const network = await state.web3_account.provider.getNetwork()
            state.web3_account.network = network
            return network;
        } else {
            state.web3_account.network = "-"
            return "-"
        }
    },
    // Updates the account balance and network
    async updateAccount({ state }, addr) {
        this.dispatch("web3_wallet/getBalance", addr, { root: true }).then(() => {
            this.dispatch("web3_wallet/getNetwork", { root: true }).then(() => {
                // Create wallet storage
                state.web3_account.wallet_address = addr || "";
                this.dispatch("web3_wallet/createWalletStorage", { root: true })
            })
        })
    },

    // ======================================================================
    // MetaMask Wallet functions 
    // ======================================================================
    // Connects MetaMask Wallet if exists
    async connectMetaMask({ state }) {
        /* Connecting to Metamask, this will 
          open the MetaMask UI, we should disable the button 
          while the request is pending
          
          MUST BE TRIGGERED BY BUTTON BY USER
          */
        state.web3_account.wallet_used = "metamask" // using MetaMask
        if (typeof window.ethereum === 'undefined') {
            console.log("Meta Mask is not installed")
            window.location.replace("dapp:" + this.site);
            this.dispatch("web3_wallet/logoutWallet", { root: true })
            setTimeout(() => {
                state.connecting_wallet = false;
                window.href = "https://metamask.io/download.html";
            }, 3000);
            state.wallet_connected = false
        }
        // Creating the MetaMask provider with Ethers
        try {
            // Creating the provider
            this.ethers = require('ethers')
            state.web3_account.provider = new this.ethers.providers.Web3Provider(
                window.ethereum,
                "any"
            );
            // Requesting the accounts
            await state.web3_account.provider.send("eth_requestAccounts", []).then((res) => {
                state.web3_account.wallet_address = res[0] || "";
                state.wallet_connected = true
                this.dispatch("web3_wallet/updateAccount", res[0], { root: true })
            });

        } catch (e) {
            const notification = {
                type: "error",
                message: "Unable to connect to Metamask! 😢",
            };
            this.dispatch("notification/add", notification, {
                root: true,
            });
            state.wallet_connected = false
        }
    },
    // Disconects the wallet info
    async logoutWallet({ state }) {
        state.wallet_connected = false
        state.web3_account = {
                wallet_address: "",
                balance: 0.0,
                network: "",
                provider: null,
                wallet_used: "",
            }
            // Deleting the wallet storage
        this.dispatch("web3_wallet/deleteWalletStorage", { root: true })

        // // Notification of success
        // const notification = {
        //     type: "success",
        //     message: "Disconnected successfully from the wallet!",
        // };
        // this.dispatch("notification/add", notification, {
        //     root: true,
        // });
    },
    // Retriwves the MetaMask address from window if exists
    async getMetamaskAddress() { //{ commit }) {
        if (typeof window.ethereum !== 'undefined') {
            const accounts = await window.ethereum.request({
                method: "eth_accounts",
            });
            return accounts[0];
        } else {
            return null;
        }
    },
    // Wallets changed listener
    async metamaskAccountsChanged({ state }) {
        // Obtain new metamask address
        this.dispatch("web3_wallet/getMetamaskAddress", { root: true }).then((addr) => {
                // If exists, update the account
                if (addr && addr !== state.web3_account.wallet_address) {
                    // Update Network, Balance, Address
                    this.dispatch("web3_wallet/updateAccount", addr, { root: true })
                    state.wallet_connected = true // Set wallet logged

                } else if (typeof addr === "undefined") {
                    this.dispatch("web3_wallet/logoutWallet", { root: true })
                    this.dispatch("web3_wallet/deleteWalletStorage", { root: true })
                    state.wallet_connected = false
                }
            })
            // Otherwise, logout the account
    },
    async metamaskChainChanged({ state }, newChain) {
        this.dispatch("web3_wallet/getMetamaskAddress", { root: true }).then((addr) => {
            // Update Network, Balance, Address
            if (addr) {
                this.dispatch("web3_wallet/updateAccount", addr, { root: true })
                state.wallet_connected = true // Set wallet logged

                if (!state.ALLOWED_NETWORK_IDS.includes(newChain)) {
                    // use the main net pls
                    this.dispatch(
                        "notification/add", {
                            type: "error",
                            message: "You are using a wrong network! Please switch to the 'Mumbai'!",
                        }, {
                            root: true,
                        }
                    );
                }
            } else {
                this.dispatch("web3_wallet/logoutWallet", { root: true })
                this.dispatch("web3_wallet/deleteWalletStorage", { root: true })
                state.wallet_connected = false
            }
        })
    },

    // ===================================
    // Main Wallet Connect
    // ===================================
    web3AccountsChanged({ state }) {
        /* If Account is changed we should proove
      if some account exists,if yes we will change it if not, 
      we will close the session*/
        // Depending of the wallet type, we will execute one or another functions
        if (state.web3_account.wallet_used === "metamask") {
            this.dispatch("web3_wallet/metamaskAccountsChanged", { root: true })
        }
    },
    web3NetworksChanged({ state }, newChain) {
        // Depending of the wallet type, we will execute one or another functions
        if (state.web3_account.wallet_used === "metamask") {
            this.dispatch("web3_wallet/metamaskChainChanged", newChain, { root: true })
        }
    },
    // When the user re-enters the web, we will check the storage if he has 
    // The account connected
    async onReEntered() {
        const wallet_storage = this.getters["web3_wallet/getWalletStorage"];
        if (wallet_storage) {
            // Proove the type of wallet
            // console.log("Wallet encontrada:", wallet_storage)
            // MetaMsk wallet
            if (wallet_storage.wallet_used === "metamask" && typeof window.ethereum !== "undefined") {
                this.dispatch("web3_wallet/connectMetaMask", { root: true })
            }
        } else {
            this.dispatch("web3_wallet/logoutWallet", { root: true })
        }
    }


}