<template>
  <div
    class="input_container"
    :class="{
      shadows: shadow,
      borders: border,
      margins: margin,
      maxWidth: max_width,
      b_right: border_right,
    }"
  >
    <div class="btn">
      <img src="@/assets/icons/navbar/search.svg" alt="" />
    </div>
    <input
      @input="checkAndSend($event.target.value)"
      type="text"
      name="search"
      placeholder="Search"
    />
  </div>
</template>

<script>
export default {
  props: {
    shadow: {
      type: Boolean,
      default: true,
    },
    border: {
      type: Boolean,
      default: true,
    },
    margin: {
      type: Boolean,
      default: true,
    },
    max_width: {
      type: Boolean,
      default: false,
    },
    border_right: {
      type: Boolean,
      default: false,
    },
  },
  name: "gf-search-input",
  data() {
    return {};
  },
  methods: {
    checkAndSend(value) {
      this.$emit("update:modelValue", value);
    },
  },
};
</script>

<style scoped>
.input_container {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  height: 45px;
  background: white;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  border-radius: 16px;
}
.shadows {
  box-shadow: 0px 0px 15px 5px rgb(37 37 37 / 3%);
}
.borders {
  border: 1px solid #ebe9e9 !important;
}
.margins {
  margin: 15px 0px 15px 0px;
}
.maxWidth {
  max-width: 500px;
}
.b_right {
  border-right: 1px solid #ebe9e9;
}
.btn {
  position: relative;
  height: 35px;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 16px;
  background-color: none;
}
.btn img {
  height: 25px;
  width: 25px;
}
.input_container input {
  position: relative;
  height: 100%;
  width: 100%;
  border-radius: 0px 16px 16px 0px;
  border: none;
  outline: none;
  font-size: 16px;
  font-weight: 400;
  color: #252525;
  overflow: hidden;
  box-sizing: border-box;
  padding: 0px 10px 0px 0px;
}
/* .input_container input:focus {
  border-right: 1px solid #ebe9e9;
} */
@media (hover: hover) {
}
</style>
