<template>
  <div class="profile_container">
    <gf-user-profile-menu :selectedOption="'My collections'" class="cont">
    </gf-user-profile-menu>
    <div class="all_container">
      <div class="central_container">
        <div class="menu">
          <div class="toolbar_menu">
            <span class="generic_text">
              In the section below you can check your NFT collection for errors
              before minting it on the blockchain.
            </span>
            <div class="sep-15-30-px"></div>
            <div class="label_text">Choose the collection data storage.</div>
            <div class="menu_cta_opc">
              <gf-select-box-img
                v-model="store_imgs_on"
                :options="image_storage_opcs"
              ></gf-select-box-img>
            </div>
            <div class="menu_ctas_btns">
              <gf-cta-button
                :label="'Mint collection'"
                @click="mintNFTCollection()"
              ></gf-cta-button>
              <gf-delete-button :label="'Delete collection'"></gf-delete-button>
            </div>
          </div>
        </div>
        {{ this.selected_collection_data }}
        <div class="subtitle_text space">Simple collection preview</div>
        <div class="banner_section">
          <img
            src="https://rarible.mypinata.cloud/ipfs/bafybeiejc6ekvjzvscqacvansc3qq2fdxvkg4iah6zti2jiia2rbykxdb4"
            alt=""
          />
          <div class="collection_logo">
            <div class="collection_relative_logo">
              <img
                src="https://assets.raribleuserdata.com/prod/v1/image/t_avatar_big/aHR0cHM6Ly9pbWFnZXMuZ29kc3VuY2hhaW5lZC5jb20vbWlzYy9ndS1jb3Zlci1waG90by0xLmpwZw=="
                alt=""
              />
            </div>
          </div>
        </div>
        <div class="title_section">
          <div class="title_text">
            {{
              this.capitalize(
                this.selected_collection_data
                  ? this.selected_collection_data.collection.name
                  : "-"
              )
            }}
          </div>
          <div
            class="edit_collection"
            @click="this.$router.push('/account/information/update')"
          >
            <img
              src="@/assets/icons/UI/writer.svg"
              class="profile_img"
              alt=""
            />
          </div>
        </div>
        <div class="description_section space">
          <div class="description_part">
            <span class="generic_text">
              {{
                this.capitalize(
                  this.cutGenDesc(
                    this.selected_collection_data
                      ? this.selected_collection_data.collection.description
                      : "-"
                  )
                )
              }}
              <span class="label_text_blue">See more</span>
            </span>
          </div>
          <div class="collection_info">
            <span class="generic_text collection_property">
              <span>Blockchain:</span>
              <b>{{
                this.selected_collection_data
                  ? this.selected_collection_data.collection.blockchain
                  : "-"
              }}</b></span
            >
            <span class="generic_text collection_property">
              <span>Payment token:</span>
              <b>{{
                this.selected_collection_data
                  ? this.selected_collection_data.collection.payment_token
                  : "-"
              }}</b></span
            >
            <span class="generic_text collection_property">
              <span>Creator fee:</span>
              <b
                >{{
                  this.selected_collection_data
                    ? this.selected_collection_data.collection.creator_fee
                    : "-"
                }}%</b
              ></span
            >
            <div class="thin_sep"></div>
            <gf-social-tags
              :show_creation="false"
              :social_networks="
                this.selected_collection_data
                  ? this.selected_collection_data.collection.social_links
                  : []
              "
            ></gf-social-tags>
          </div>
        </div>
        <div class="toolbar">
          <div class="nft_scroller">
            <div class="creator_wrapper">
              <div class="creator_scroller">
                <div class="creator_content" ref="creator_content">
                  <div
                    class="creator_menu_item active"
                    @click="setOption('NFT Items')"
                  >
                    <span class="link_title">NFT Items</span>
                    <span class="link_number">
                      {{
                        this.selected_collection_data
                          ? this.selected_collection_data.nfts.length
                          : 0
                      }}</span
                    >
                  </div>
                  <div class="creator_menu_item" @click="setOption('Activity')">
                    <span class="link_title"> Activity</span>
                    <!-- <span class="link_number">
                      {{ this.my_server_nfts_number }}
                      </span
                    > -->
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="reload">
            <img
              @click="
                getUserNFTs();
                reloadCollections();
              "
              src="@/assets/icons/navbar/reload.svg"
              alt=""
            />
          </div>
          <div class="menu_filter" @click="this.toggleFilterState()">
            <img
              v-if="$store.state.navbar_state.dark_mode === true"
              src="@/assets/icons/navbar/filter_dark.svg"
              alt=""
            />
            <img v-else src="@/assets/icons/navbar/filter.svg" alt="" />
            <span v-if="this.filter_active === false">Show filter</span>
            <span v-if="this.filter_active === true">Quit filter</span>
          </div>
        </div>
        <div class="toolbar_filter" v-if="this.filter_active">
          <div class="tf_search">
            <gf-search-input v-model="valor"></gf-search-input>
          </div>
        </div>
        <!--ACTIVITY SECTION -->
        <div
          class="on_sale_section"
          v-if="this.creator_menu.selected_option === 'Activity'"
        >
          <div class="card_section">
            <div class="soon">
              <img src="@/assets/icons/soon.svg" alt="" />
              <span class="generic_text">
                The functionality will be added soon...
              </span>
            </div>
          </div>
        </div>
        <!-- NFT ITEMS SECTION -->
        <div
          class="created_section"
          v-if="this.creator_menu.selected_option === 'NFT Items'"
        >
          <div class="card_section">
            <gf-nft-card
              v-for="(nft, key) in this.selected_collection_data
                ? this.selected_collection_data.nfts
                : []"
              :key="key"
              :nft_data="nft"
              :card_id="key"
            ></gf-nft-card>
          </div>
        </div>
      </div>
    </div>
  </div>
  <fut></fut>
</template>

<script>
import fut from "@/components/Footer";
import axios from "axios";
export default {
  components: {
    fut,
  },
  data() {
    return {
      filter_active: false,
      creator_menu: {
        selected_option: "NFT Items",
        owned: 0,
        posted: 0,
        created: 0,
      },
      minted_nfts: false,
      nft_data: {
        posted_nfts: [],
        created_nfts: [],
      },
      // Collection data storage
      store_imgs_on: null,
      image_storage_opcs: {
        0: {
          network: "Off-chain (Grifenix-DB)",
          url: "assets/icons/data_storage/grifenix_db.png",
        },
        1: {
          network: "On-chain (Polygon) - [Coming soon]",
          url: "assets/icons/blockchains/polygon.png",
        },
        2: {
          network: "On-chain (IPFS) - [Coming soon]",
          url: "assets/icons/data_storage/ipfs.png",
        },
      },
    };
  },
  methods: {
    reloadCollections() {
      this.$store.dispatch("account_data/getUserServerCollections", {
        root: true,
      });
    },
    getUserNFTs() {
      this.$store.dispatch("account_data/getUserServerNFTs", {
        root: true,
      });
    },
    toggleFilterState() {
      this.filter_active = !this.filter_active;
    },
    formatWalletAddress(addr) {
      let ad = "";
      try {
        let ini = addr.substring(0, 6);
        let end = addr.substring(addr.length - 4, addr.length);
        ad = ini + " *** " + end;
      } catch (e) {
        ad = "-";
      }
      return ad;
    },
    capitalize(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    cutGenDesc(text) {
      if (text && text.length > 250) {
        return text.substring(0, 250) + "...";
      } else {
        return text;
      }
    },
    setOption(option) {
      let links = this.$refs.creator_content.children;
      for (let i = 0; i < links.length; i++) {
        links[i].classList.remove("active");
        // Adding to the actived link
        option = option.trim();
        // Adding active class
        let title = links[i].children[0].innerHTML.trim();
        if (title === option) {
          this.creator_menu.selected_option = option;
          links[i].classList.add("active");
        }
      }
    },
    scrollToTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    },
    getImageExtension(contentType) {
      return this.$store.state.constants.typeToExtension[contentType] || null;
    },
    mintingComingSoon(where) {
      const notification = {
        type: "information",
        message:
          "Possibility of storing data on " +
          where +
          " coming soon, stay tuned! ",
      };
      this.$store.dispatch("notification/add", notification, {
        root: true,
      });
    },
    async downloadFileFromURL() {
      // Change the url and set the url from parameter
      let url =
        "https://grifenixs3.s3.amazonaws.com/digest_media/digest_groups/40799123-be21-4ad2-8747-68292a807461.digest_cover_proto222.png";
      const response = await fetch(url);
      const contentType = response.headers.get("content-type");
      const blob = await response.blob();
      console.log("Blob:", blob);
      const file = new File(
        [blob],
        "img" + this.getImageExtension(contentType),
        { contentType }
      );
      console.log("File:", file);

      // Returning the file
      if (blob) {
        return file;
      } else {
        return null;
      }
    },
    checkImgExtension(url) {
      let ext = url.split(".").at(-1);
      if (this.$store.state.constants.imgAllowedTypes.includes(ext)) {
        return true;
      } else {
        return false;
      }
    },
    // ================================================
    // Minting options
    // ================================================
    async mintNFTCollectionOnGrifenixDB() {
      console.log(this.selected_collection_data.collection);
      // Preparing the info
      const fd = new FormData();
      const logo = await this.downloadFileFromURL(
        this.selected_collection_data.collection.banner
      );
      fd.append("coll_logo", logo, logo.name);
      const banner = await this.downloadFileFromURL(
        this.selected_collection_data.collection.banner
      );
      fd.append("coll_banner", banner, banner.name);
      fd.append("coll_name", this.selected_collection_data.collection.name);
      fd.append(
        "coll_description",
        this.selected_collection_data.collection.description
      );
      fd.append(
        "coll_social_links",
        this.selected_collection_data.collection.social_links
      );
      // Adding NFT data
      const nfts = await this.selected_collection_data.nfts;
      console.log("NFTS:", nfts);
      fd.append("max_supply", nfts.length);
      for (let i = 0; i < nfts.length; i++) {
        const nft_image = await this.downloadFileFromURL(nfts[i].image);
        fd.append("item_image_" + i, nft_image, i + "_" + nft_image.name);
        fd.append("name_" + i, nfts[i].title);
        fd.append("description_" + i, nfts[i].description);
        fd.append("external_link_" + i, nfts[i].link);
        fd.append("attributes_" + i, nfts[i].properties);
      }

      // Sending the info to the server storage
      axios.defaults.headers.common["Authorization"] =
        "Token " + this.$store.state.user_auth_w2.token;
      const url = "http://192.168.0.21:8000/post_gdb_simple_collection";
      let response = await axios.post(url, fd);
      console.log("Response: ", response.data);

      // Checking if the images and data were uploaded perfectly
      let cLog = this.checkImgExtension(response.data.logo);
      let cBan = this.checkImgExtension(response.data.banner);
      let cIMG = true;
      for (let i = 0; i < Number(response.data.max_supply); i++) {
        cIMG = cIMG && this.checkImgExtension(response.data.nft_links[i]);
      }
      const imagesCheck = (await cLog) && cBan && cIMG;
      // Minting the NFT for the user
      if (imagesCheck === true) {
        console.log("Minting the contract");
      } else {
        console.log("Something went wrong on the server side");
      }
    },
    async mintNFTCollectionOnIPFS() {
      console.log("IPFS minting coming soon");
      this.mintingComingSoon("IPFS");
    },
    async mintNFTCollectionOnPolygonChain() {
      console.log("Polygon minting coming soon");
      this.mintingComingSoon("Polygon");
    },
    // Starts the minting process of the collection on the GDB
    async mintNFTCollection() {
      // Will call the user wallet to mint the collection, so, be carefull
      const walletConnected =
        this.$store.getters["web3_wallet/isWalletConnected"];
      if (walletConnected) {
        if (this.store_imgs_on === "Off-chain (Grifenix-DB)") {
          this.mintNFTCollectionOnGrifenixDB();
        } else if (
          this.store_imgs_on === "On-chain (Polygon) - [Coming soon]"
        ) {
          this.mintNFTCollectionOnPolygonChain();
        } else if (this.store_imgs_on === "On-chain (IPFS) - [Coming soon]") {
          this.mintNFTCollectionOnIPFS();
        } else {
          this.$store.dispatch(
            "notification/add",
            { type: "error", message: "Please, choose the data storage" },
            {
              root: true,
            }
          );
        }
      } else {
        this.$store.dispatch(
          "notification/add",
          { type: "error", message: "Please, connect your wallet!" },
          {
            root: true,
          }
        );
      }
    },
  },
  created() {
    if (this.$store.state.account_data.user_server_collections) {
      this.creator_menu.my_collections =
        this.$store.state.account_data.user_server_collections.length;
    } else {
      this.creator_menu.my_collections = 0;
    }
    this.scrollToTop();
    if (!this.selected_collection_data) {
      this.$router.push("/account/nft/dashboard");
    }
  },
  computed: {
    selected_collection_data() {
      let collection_id =
        this.$store.state.account_data.nftForCollectionSelected;
      if (collection_id == -1) {
        return null;
        // this.$router.push("/account/nft/dashboard");
      }
      // Obtaining the collection data
      let collection_data = {};
      for (
        let i = 0;
        i < this.$store.state.account_data.user_server_collections.length;
        i++
      ) {
        if (
          this.$store.state.account_data.user_server_collections[i].id ===
          collection_id
        ) {
          collection_data =
            this.$store.state.account_data.user_server_collections[i];
        }
      }
      // Obtaining the NFTs data fo the selected collection
      let collection_nfts = [];
      for (
        let i = 0;
        i < this.$store.state.account_data.user_server_nfts.length;
        i++
      ) {
        if (
          this.$store.state.account_data.user_server_nfts[i].collection ===
          collection_id
        ) {
          collection_nfts.push(
            this.$store.state.account_data.user_server_nfts[i]
          );
        }
      }
      return {
        collection: collection_data,
        nfts: collection_nfts,
      };
    },
  },
};
</script>

