<template>
  <div class="social_container">
    <div
      v-for="(netw, key) in this.networks"
      :key="key"
      class="social_link"
      @click="goto(netw.network_url)"
    >
      <div class="img_container">
        <img
          :src="require('./../../../' + this.getIMGLink(netw.network))"
          alt=""
          class="sn_imgs"
        />
      </div>
    </div>
    <span
      v-if="this.networks.length === 0 && this.show_creation === true"
      class="generic_text"
      >Oh, dear Web3 🥰! But if you want you can update your social networks
      <span class="label_text_blue">here</span></span
    >
    <span
      class="generic_text"
      v-if="this.networks.length === 0 && this.show_creation === false"
    >
      -
    </span>
  </div>
</template>

<script>
export default {
  name: "gf-social-tags",
  props: {
    show_creation: {
      type: Boolean,
      default: true,
    },
    social_networks: String,
  },
  data() {
    return {
      network_img: {
        instagram: "assets/icons/social_networks/instagram.png",
        discord: "assets/icons/social_networks/discord.png",
        link: "assets/icons/social_networks/link.png",
        linkedin: "assets/icons/social_networks/linkedin.png",
        looksrare: "assets/icons/social_networks/looksrare.png",
        medium: "assets/icons/social_networks/medium.png",
        opensea: "assets/icons/social_networks/opensea.png",
        twitter: "assets/icons/social_networks/twitter.png",
        website: "assets/icons/social_networks/website.png",
        telegram: "assets/icons/social_networks/telegram.png",
        reddit: "assets/icons/social_networks/reddit.png",
        rarible: "assets/icons/social_networks/rarible.png",
      },
      networks: [
        { network: "instagram", network_url: "a" },
        { network: "discord", network_url: "b" },
      ],
    };
  },
  watch: {
    social_networks: function (newVal) {
      this.networks = this.formateTags(newVal);
    },
  },
  mounted() {
    // this.networks =
    this.networks = this.formateTags(this.social_networks);
  },
  methods: {
    goto(link) {
      window.open(link, "_blank");
    },
    getIMGLink(imgLink) {
      let url = this.network_img[imgLink];
      if (url != undefined) {
        return url;
      } else {
        return "assets/icons/social_networks/link.png";
      }
    },
    formateTags(nets) {
      try {
        return JSON.parse(nets);
      } catch (e) {
        // return [{ "-": "" }];
        this.networks = [];
        return [];
      }
    },
  },
};
</script>

<style scoped>
.generic_text {
  color: #6e6e73;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
}
.label_text_blue {
  color: #0071da;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}
.social_container {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: fit-content;
  box-sizing: border-box;
  justify-content: center;
  text-align: center;
  padding: 5px 20px;
  width: 100%;
  gap: 20px;
}
.social_link {
  position: relative;
  height: 25px;
  width: 25px;
  cursor: pointer;
  margin: 0px 0px 5px 0px;
}
.img_container {
  position: relative;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  padding: 3px 0px 0px 0px;
  transition-duration: 0.3s;
}
.sn_imgs {
  position: relative;
  height: 25px;
  width: 25px;
}
@media (hover: hover) {
  .img_container:hover {
    padding: 0px 0px 3px 0px;
    transition-duration: 0.3s;
  }
}
</style>
