<template>
  <section
    class="settings"
    :class="{ darkMode: $store.state.navbar_state.dark_mode === true }"
  >
    <div class="settings_vue">
      <gf-settings-menu
        :selected_link="'Update account information'"
      ></gf-settings-menu>
      <div class="container_for_settings">
        <div class="settings_container">
          <div class="update_info_container">
            <div class="update_container">
              <div class="logo_img">
                <img src="@/assets/icons/grifenix/profile_blue.svg" alt="" />
              </div>
              <h1 class="title_text">Update account</h1>
              <div class="thin_sep"></div>
              <div class="known">
                <div class="properties">
                  <span class="label_text"> Username </span>
                  <span class="generic_text">{{
                    this.capitalize(
                      this.$store.state.account_data.data.username
                    )
                  }}</span>
                </div>
                <div class="properties">
                  <span class="label_text"> Email </span>
                  <span class="generic_text">{{
                    this.setEmail(this.$store.state.account_data.data.email)
                  }}</span>
                </div>
              </div>
              <div class="thin_sep"></div>
              <gf-input-field
                v-model="account.firstname"
                :text_type="'text'"
                :showError="e_firstname"
                :showRequired="false"
                @input="reset()"
              >
                <template v-slot:title>First name</template>
                <template v-slot:helper>e.g Michaela</template>
              </gf-input-field>
              <gf-input-field
                v-model="account.lastname"
                :text_type="'text'"
                :showError="e_lastname"
                :showRequired="false"
                @input="reset()"
              >
                <template v-slot:title>Last name</template>
                <template v-slot:helper>e.g Roxburgh</template>
              </gf-input-field>

              <gf-textarea
                v-model="account.description"
                :showError="e_description"
                :showRequired="false"
              >
                <template v-slot:title>Description</template>
                <template v-slot:helper>E.g. "We provide you...."</template>
              </gf-textarea>
              <gf-tag-input-field
                v-model="account.thematics"
                :showError="e_thematics"
                :setTags="thematics"
              >
                <template v-slot:title>Favorite Thematics</template>
                <template v-slot:helper>Eg: Nature photography.</template>
              </gf-tag-input-field>
              <div class="cta">
                <gf-action-button
                  class="login"
                  @click="updateInformation()"
                  :label="'Update account'"
                ></gf-action-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
  <fut></fut>
</template>

<script>
import axios from "axios";
import fut from "@/components/Footer";
export default {
  components: {
    fut,
  },
  data() {
    return {
      filter_active: false,
      thematics: "",
      account: {
        firstname: "",
        lastname: "",
        description: "",
        thematics: "",
      },
      e_firstname: false,
      e_lastname: false,
      e_description: false,
      e_thematics: false,
    };
  },
  methods: {
    capitalize(string) {
      try {
        return string.charAt(0).toUpperCase() + string.slice(1);
      } catch (e) {
        return "-";
      }
    },
    setEmail(email) {
      if (email && email.length > 0) {
        return email;
      } else {
        return "-";
      }
    },
    goto(link) {
      //window.location.href = link;
      window.open(link, "_blank");
    },
    scrollToTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    },
    reset() {
      this.e_firstname = false;
      this.e_lastname = false;
      this.e_description = false;
    },
    maxSizeReached() {
      const notification = {
        type: "error",
        message: "Max size reached!",
      };
      this.$store.dispatch("notification/add", notification, {
        root: true,
      });
    },
    async updateInformation() {
      const bodyData = new FormData();
      bodyData.append("first_name", this.account.firstname);
      bodyData.append("last_name", this.account.lastname);
      bodyData.append("description", this.account.description);
      bodyData.append(
        "favorite_thematics",
        JSON.stringify(this.account.thematics)
      );
      const token = this.$store.state.user_auth_w2.token;
      const url = this.$store.state.gf_contracts.backend;
      axios.defaults.headers.common["Authorization"] = "Token " + token;
      await axios.put(url + "/changeinfo/", bodyData).then((res) => {
        console.log("EEE:", res);
        this.reset();

        const notification = {
          type: "success",
          message: "Your account information was changed successfully!",
        };
        this.$store.dispatch("notification/add", notification, {
          root: true,
        });
        this.$router.push("/account");
      });
    },
    updateAccountData() {
      try {
        this.thematics = this.$store.state.account_data.data.thematics;
        this.account.thematics = this.thematics;
        this.account.firstname = this.$store.account_data.data.firstname;
        this.account.lastename = this.$store.account_data.data.lastname;
        this.account.description = this.$store.account_data.data.description;
      } catch (e) {
        this.thematics = "";
      }
    },
    loadUserData() {
      this.$store.dispatch("account_data/getUserData", {
        root: true,
      });
    },
  },
  mounted() {
    this.scrollToTop();
    this.loadUserData();
    this.updateAccountData();
  },
  computed: {
    userData() {
      return this.$store.state.account_data.data;
    },
  },
  watch: {
    userData(newVal) {
      try {
        this.thematics = newVal.thematics;
        this.account.thematics = this.thematics;
        this.account.firstname = newVal.firstname;
        this.account.lastname = newVal.lastname;
        this.account.description = newVal.description;
      } catch (e) {
        this.thematics = "";
        this.account.thematics = "";
      }
    },
  },
  beforeCreate() {
    // this.$store.commit("user_auth_w2/initializeStore");
    this.$store.dispatch("user_auth_w2/initializeStoreIfExists", {
      root: true,
    });
    const token = this.$store.state.user_auth_w2.token;
    if (token) {
      axios.defaults.headers.common["Authorization"] = "Token " + token;
    } else {
      axios.defaults.headers.common["Authorization"] = "";
      //   this.$store.commit("user_auth_w2/removeToken");
      this.$store.dispatch("user_auth_w2/removeToken", {
        root: true,
      });
    }
    // if (!this.$store.state.user_auth_w2.isAuthentificated) {
    //   this.$router.push("/");
    // }
  },
};
</script>

<style scoped>
.red {
  color: #bb0a21 !important;
}
