<template :class="{ darkMode: $store.state.navbar_state.dark_mode === true }">
  <div class="account_filter_close" @click="showFilter()">
    <img
      src="@/assets/icons/navbar/filter_dark.svg"
      alt=""
      v-if="$store.state.navbar_state.dark_mode === true"
    />
    <img src="@/assets/icons/navbar/filter.svg" alt="" v-else />
    <h2 v-if="!filter_active">Show settings</h2>
    <h2 v-else class="red">Hide settings</h2>
  </div>
  <div class="settings_filter" :class="{ filter_show: filter_active }">
    <div
      class="general_text_title_main filter_link active"
      @click="this.$router.push('/account/settings')"
    >
      General settings
    </div>
    <div class="line_sep"></div>
    <div class="general_text_title">Account settings</div>
    <div class="general_text_menu">
      <h2
        class="filter_link"
        @click="this.$router.push('/account/information/update')"
      >
        Update account information
      </h2>
      <h2
        class="filter_link"
        @click="this.$router.push('/account/password/update')"
      >
        Update password
      </h2>
    </div>
    <div class="line_sep"></div>
    <gf-social-icons></gf-social-icons>
  </div>
</template>

<script>
export default {
  name: "gf-settings-menu",
  props: {
    selected_link: String,
  },
  data() {
    return {
      filter_active: false,
      links: [],
    };
  },
  methods: {
    showFilter() {
      this.filter_active = !this.filter_active;
    },
    setSelectedLink() {
      this.links = document.getElementsByClassName("filter_link");
      for (let i = 0; i < this.links.length; i++) {
        if (this.links.item(i).innerHTML.trim() === this.selected_link.trim()) {
          this.links.item(i).classList.add("selected_link");
        }
      }
    },
  },
  mounted() {
    this.setSelectedLink();
  },
};
</script>

<style scoped>
.red {
  color: #bb0a21 !important;
}
.selected_link {
  color: #0071da !important;
  transition-duration: 0.5s;
}
.filter_link {
  transition-duration: 0.5s;
}
.filter_show {
  opacity: 1 !important;
  display: block !important;
}
.line_sep {
  height: 1px;
  width: 100%;
  background-color: #ebe9e9;
  margin: 20px 0px 20px 0px;
}
.settings_filter {
  position: sticky;
  top: 110px;
  background: white;
  height: fit-content;
  width: 100%;
  /* max-height: 80vh; */
  max-width: 300px;
  box-sizing: border-box;
  padding: 0px 30px 0px 0px;
  overflow-y: auto;
  transition-duration: 0.3s;
}
.general_text_title_main,
.general_text_title {
  font-size: 16px;
  color: #252525;
  font-weight: 500;
  box-sizing: border-box;
  padding: 0px 0px 0px 10px;
}
.general_text_title_main {
  font-weight: 600;
  cursor: pointer;
}
.general_text_menu {
  box-sizing: border-box;
  padding: 0px 0px 0px 10px;
  margin: 20px 0px 0px 0px;
}
.general_text_menu h2 {
  font-size: 16px;
  color: #6e6e73;
  font-weight: 500;
  line-height: 45px;
}
.account_filter_close {
  z-index: 9999;
  margin: 0px 0px 10px 0px;
  opacity: 0;
  display: none;
}

@media (max-width: 950px) {
  .settings_filter {
    position: relative;
    z-index: 999;
    top: 0px;
    left: 0px;
    height: fit-content;
    width: 100%;
    box-sizing: border-box;
    padding: 20px 5px 10px 5px !important;
    max-width: 100%;
    opacity: 0;
    display: none;
    border: 1px solid #f2f2f2;
    padding: 5px;
    border-radius: 16px;
    margin: 0px 0px 20px 0px;
    background: #f2f2f2;
    transition-duration: 0.3s;
  }
  .account_filter_close {
    opacity: 1;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    box-sizing: border-box;
    padding: 10px;
    left: 0px;
    top: 0px;
    z-index: 999;
    height: 50px;
    width: 100%;
    cursor: pointer;
  }
  .account_filter_close h2 {
    font-size: 16px;
    font-weight: 500;
    margin: 0px 0px 0px 10px;
    color: #252525;
  }
  .account_filter_close img {
    margin: 0px 0px 0px 0px;
    height: 21px;
    width: 21px;
  }
  .line_sep {
    background-color: #e2e2e2;
  }
}
@media (hover: hover) {
  .general_text_menu h2:hover {
    font-weight: 600;
    color: #0071da;
    cursor: pointer;
  }
  .account_filter_close:hover h2 {
    color: #0071da;
  }
}
.darkMode .filter_link {
  color: #f2f2f2;
}
.darkMode .general_text_title_main {
  color: white;
}
.darkMode .active {
  color: white !important;
}
.darkMode .settings_filter {
  background-color: #00162a !important;
}
.darkMode .account_filter_close h2 {
  color: white;
}
</style>
