<script>
import axios from "axios";
// import { mixin as clickaway } from "vue3-click-away"; //"vue-clickaway";
import { mixin as VueClickAway } from "vue3-click-away";
export default {
  mixins: [VueClickAway],
  data() {
    return {
      dark_mode: false,
      toggle_button: false,
      langDropVisible: false,
      profileDropVisible: false,
      accountDropVisible: false,
      nav_pc_balance: "Balance: 0.0",
      address: "-",
      full__address: "",
      copied: false,
      navbar__refs: ["nav_link_0", "nav_link_1", "nav_link_2"],
    };
  },
  created() {
    window.addEventListener("scroll", this.nav_scroll);
  },
  mounted() {
    this.dark_mode = JSON.parse(localStorage.getItem("nightMode"));
  },
  unmounted() {
    window.removeEventListener("scroll", this.nav_scroll);
  },
  methods: {
    goToLink(path) {
      window.open(path, "_blank");
    },
    copy__address() {
      try {
        navigator.clipboard
          .writeText(this.$store.state.user_account.wallet_account.address)
          .then(
            () => {
              this.copied = true;
              setTimeout(() => {
                this.copied = false;
              }, 2000);
            },
            () => {
              this.copied = false;
            }
          );
      } catch (e) {
        this.copied = false;
        let v =
          "Your address is: \n" +
          this.$store.state.user_account.wallet_account.address;
        alert(v);
      }
    },
    capitalize(word) {
      const lower = word.toLowerCase();
      return word.charAt(0).toUpperCase() + lower.slice(1);
    },
    change__active(link) {
      // this.navbar__refs
      try {
        this.$store.state.navbar_state.navbar_positions.forEach((element) => {
          this.$refs[element].classList.remove("nav__links__active");
          // this.$refs[element + "_mobile"].classList.remove("nav__links__active");
        });
        if (link != "") {
          this.$refs[link].classList.add("nav__links__active");
          // this.$refs[link + "_mobile"].classList.add("nav__links__active");
        }
      } catch (e) {
        console.log("");
      }
    },
    toggleMenu() {
      this.$refs.nav__mobile.classList.toggle("active");
      if (this.toggle_button) {
        this.toggle_button = false;
        this.$refs.nav__toggle.classList.remove("active__toggle");
        this.$refs.nav__mobile__lang.classList.remove("active");
        this.$refs.nav__mobile__profile.classList.remove("active");
        this.$refs.nav__mobile__wallet.classList.remove("active");
      } else {
        this.toggle_button = true;
        this.$refs.nav__toggle.classList.add("active__toggle");
        this.$refs.nav__mobile__lang.classList.remove("active");
        this.$refs.nav__mobile__profile.classList.remove("active");
        this.$refs.nav__mobile__wallet.classList.remove("active");
      }
    },
    toggleLangMenu() {
      this.$refs.nav__mobile__lang.classList.toggle("active");
    },
    toggleLangPC() {
      this.langDropVisible = !this.langDropVisible;
    },
    toggleProfilePC() {
      this.profileDropVisible = !this.profileDropVisible;
    },
    toggleAccountPC() {
      this.accountDropVisible = !this.accountDropVisible;
    },
    toggleAccountMenu() {
      this.$refs.nav__mobile__profile.classList.toggle("active");
    },
    toggleWalletMenu() {
      this.$refs.nav__mobile__wallet.classList.toggle("active");
      // Update account (to update balance)
      this.$store.dispatch("user_account/updateAccount", "metamask", {
        root: true,
      });
    },
    nav_scroll() {
      if (window.scrollY > 50) {
        this.$refs.nav.classList.add("nav__active");
      } else {
        this.$refs.nav.classList.remove("nav__active");
      }
    },
    logOutWallet() {
      this.$refs.nav__mobile__wallet.classList.toggle("active");
      this.accountDropVisible = false;
      this.$store.dispatch("web3_wallet/logoutWallet", null, { root: true });
      // Notification of success
      const notification = {
        type: "success",
        message: "Disconnected successfully from the wallet!",
      };
      this.$store.dispatch("notification/add", notification, {
        root: true,
      });
    },
    logout_admin() {
      const token = this.$store.state.user_auth_w2.token;
      if (token) {
        axios.defaults.headers.common["Authorization"] = "Token " + token;
        try {
          axios
            .post(this.$store.state.gf_contracts.backend + "/logoutall/")
            .then(() => {
              const notification = {
                type: "success",
                message: "Logged out successfully",
              };
              this.$store.dispatch("notification/add", notification, {
                root: true,
              });
              //this.$store.commit("user_auth_w2/removeToken");
              this.$store.dispatch("user_auth_w2/removeToken", { root: true });
              this.$router.push("/accounts/login");
            })
            .catch(() => {
              const notification = {
                type: "error",
                message: "Something went wrong",
              };
              this.$store.dispatch("notification/add", notification, {
                root: true,
              });
              this.$store.dispatch("user_auth_w2/removeToken", {
                root: true,
              });
            });
        } catch (e) {
          this.$store.dispatch("user_auth_w2/removeToken", {
            root: true,
          });
        }
      }
    },
    setDarkMode(mode) {
      let newMode = mode.target.checked;
      // this.$store.dispatch("navbar_state/updateDarkMode", newMode, {
      //   root: true,
      // });
      this.$store.state.navbar_state.dark_mode = newMode;
    },
  },

  watch: {
    dark_mode: function () {
      localStorage.setItem("nightMode", JSON.stringify(this.dark_mode));
      this.$store.state.navbar_state.dark_mode = this.dark_mode;
    },
  },
  computed: {
    getCurrentWalletBalance() {
      const balance = (
        this.$store.state.web3_wallet.web3_account.balance /
        10 ** 18
      ).toFixed(4);
      const symbol =
        this.$store.state.web3_wallet.symbol[
          this.$store.state.web3_wallet.web3_account.network.chainId
        ] || "-ETH?";
      return balance + " " + symbol;
    },
    getNetworkName() {
      let netName = this.$store.state.web3_wallet.web3_account.network.name;
      if (netName) {
        netName = this.capitalize(netName);
      }
      return netName;
    },
    onlyAdmin() {
      const allowed_roles = ["SUPERUSER", "ADMIN"];
      if (allowed_roles.includes(this.$store.state.account_data.data.role)) {
        return true;
      } else {
        return false;
      }
    },
  },
};
</script>

