<template>
  <div class="menu_container" ref="user_menu">
    <div
      class="user_burger_m"
      ref="userBurgerMenu"
      @click="toggleAccountMenu()"
    >
      <div class="burger_user_menu"></div>
      <div class="generic_text">My account</div>
    </div>
    <div class="line_sep"></div>
    <div class="link_container">
      <div class="menu_item active" @click="this.$router.push('/account')">
        <img
          src="https://grifenix-com.github.io/G-Images/icons/toolbar/home.svg"
          alt=""
        />
        <div class="generic_text">Home</div>
      </div>
      <div
        class="menu_item"
        @click="this.$router.push('/account/nft/dashboard')"
      >
        <img
          src="https://grifenix-com.github.io/G-Images/icons/toolbar/collections.png"
          alt=""
        />
        <div class="generic_text">My collections</div>
      </div>
      <div class="menu_item" @click="this.$router.push('/account/nft/builder')">
        <img
          src="https://grifenix-com.github.io/G-Images/icons/toolbar/edit.svg"
          alt=""
        />
        <div class="generic_text">NFT builder</div>
      </div>
      <div class="menu_item inactive">
        <img
          src="https://grifenix-com.github.io/G-Images/icons/toolbar/billing_black.svg"
          alt=""
        />
        <div class="generic_text">Billing dashboard</div>
      </div>
      <div class="menu_item" @click="this.$router.push('/account/settings')">
        <img
          src="https://grifenix-com.github.io/G-Images/icons/toolbar/settings.svg"
          alt=""
        />
        <div class="generic_text">Settings</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "gf-user-profile-menu",
  props: {
    selectedOption: {
      type: String,
      default: "Home",
    },
    userMenuActive: {
      type: Boolean,
      default: false,
    },
    options: {
      home: 0,
      settings: 3,
    },
  },
  data() {
    return {};
  },
  methods: {
    toggleAccountMenu() {
      let userValue = this.$store.state.navbar_state.userMenuActive;
      if (userValue) {
        this.$refs.user_menu.classList.remove("user_menu_active");
        this.$refs.userBurgerMenu.classList.remove("active_burger_menu");
      } else {
        this.$refs.user_menu.classList.add("user_menu_active");
        this.$refs.userBurgerMenu.classList.add("active_burger_menu");
      }
      userValue = !userValue;
      this.$store.dispatch("navbar_state/setUserMenuValue", userValue);
    },
    setSelectedOption(option) {
      let links = document.getElementsByClassName("menu_item");
      for (let i = 0; i < links.length; i++) {
        option = option.trim();
        links[i].classList.remove("active");
        // Adding active links
        let title = links[i].children[1];

        if (title.innerText.trim() === option) {
          links[i].classList.add("active");
        }
      }
    },
    scrollToTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    },
  },
  //   mounted() {
  //     this.setSelectedOption(this.selectedOption);
  //   },
  mounted: function () {
    this.$nextTick(function () {
      // Code that will run only after the
      // entire view has been rendered
      this.setSelectedOption(this.selectedOption);
    });
  },
  created() {
    this.scrollToTop();
  },
};
</script>

