export const namespaced = true

export const state = {
    ALLOWED_IMG_TYPE: [
        "image/jpeg",
        "image/jpg",
        "image/png",
        "image/gif",
        "image/svg+xml",
    ],
    TYPE_TO_EXTENSION: {
        "image/jpeg": ".jpeg",
        "image/jpg": ".jpg",
        "image/png": ".png",
        "image/gif": ".gif",
        "image/svg+xml": ".svg",
    },
    ALLOWED_EXTENSIONS: ["jpeg", "jpg", "png", "svg"],
    MAX_FILE_SIZE: 10000000, // KB
    defaultImageURL: ""
}

export const getters = {
    // -> getters['posts/getX']
}

export const mutations = {
    // -> commit('posts/mutationX') Synchronous

}

export const actions = {
    // -> dispatch('posts/actionX') Asynchronous

    // async changeFavoriteTemplates({ state, rootState }, templateID) {

    // }

    // ===================================
    // Image functions 
    // ===================================
    getImageFromURL({ state }, url) {
        return new Promise(() => {
            if (!url) {
                this.file = null;
                return null;
            }
            try {
                fetch(url).then(async(response) => {
                    console.log("Response inside:", response);
                    const contentType = response.headers.get("content-type");
                    const blob = await response.blob();
                    let item = state.TYPE_TO_EXTENSION[contentType]
                    if (typeof item === "undefined") {
                        // Type not allowed
                        const notification = {
                            type: "error",
                            message: "The given type is not allowed",
                        };
                        this.$store.dispatch("notification/add", notification, {
                            root: true,
                        });
                        return null
                    }
                    const file = new File([blob], "image" + item, { contentType }) || null;
                    console.log("Blob inside:", blob);
                    console.log("Cont type:", contentType, file);

                    // Returning the file
                    // if (blob) {
                    // } else {
                    //     this.file = null;
                    //     this.$emit("update:modelValue", file);
                    // }

                    if (!file) {
                        console.log("no file")
                        return null // There is no file
                    }
                    // Too large size
                    if (file.size > state.MAX_FILE_SIZE) {
                        // Allowed extension
                        const msg = `File is too large. Max size is ${
                        state.MAX_FILE_SIZE / 1000000
                      }MB`;
                        const notification = {
                            type: "error",
                            message: msg,
                        };
                        this.$store.dispatch("notification/add", notification, {
                            root: true,
                        });
                    }
                    // return "file"
                    console.log("FILE:", file)
                    return file
                });
            } catch (e) {
                console.log("Imposible to download the image");
                return null
            }
        })
    }
}