<template>
  <div
    class="whitelist_external_container"
    :class="{ no_scroll: show_delete_popup }"
  >
    <div class="whitelist_container">
      <g-admin-dashboard-side-menu></g-admin-dashboard-side-menu>
      <div class="whitelist_main_container">
        {{ this.selected }}
        <div class="toobar_whitelist">
          <div class="email_input">
            <gf-input-field
              :noMargin="true"
              :help="false"
              :showError="this.email_error"
              @input="resetErrors()"
              v-model="this.new_email"
              ><template v-slot:title
                >Email to be added to the whitelist</template
              ></gf-input-field
            >
          </div>
          <div class="role_selector">
            <gf-select-box
              :label_text="'Select role'"
              :options="this.role_options"
              :no_margin="'true'"
              v-model="this.new_role"
            ></gf-select-box>
          </div>
          <div class="role_selector">
            <gf-select-box-id
              v-model="this.new_ttl"
              :label_text="'Token TTL'"
              :options="this.ttl_options"
              :s_opc="'1'"
              :noMargin="'true'"
            ></gf-select-box-id>
          </div>
          <gf-cta-button
            class="btn_new"
            :label="'Add to whitelist'"
            @click="this.createNewWhitelistedUser()"
          ></gf-cta-button>
        </div>
        <div class="whitelisted_accounts" v-if="filteredWhitelist.length > 0">
          <div
            class="whitelisted_account"
            v-for="(account, key) in filteredWhitelist"
            :key="key"
          >
            <div class="rol_label">
              <gf-role-tag :role="account.role"></gf-role-tag>
            </div>
            <div class="whitelist_data">
              <div class="generic_text">
                <span class="label_text">User:</span>
                {{ account.email }}
              </div>
              <div class="generic_text">
                <span class="label_text">Expires on:</span>
                {{ this.timestampToDate(account.expiracy) }}
              </div>
              <div class="generic_text mailed">
                <span class="label_text">Sent:</span>
                <gf-yesno-tag :option="account.email_sent"></gf-yesno-tag>
              </div>
            </div>
            <div class="ctas">
              <g-action-list-btn
                :label="'Send Mail'"
                @click="this.sendEmail(key)"
              ></g-action-list-btn>
              <g-delete-list-btn
                :label="'Delete'"
                @click="this.deleteWhitelistItem(key)"
              ></g-delete-list-btn>
            </div>
          </div>
        </div>
        <div class="no_whitelist" v-if="filteredWhitelist.length === 0">
          <span>There are no whitelisted accounts... 🕸</span>
        </div>
      </div>
    </div>
  </div>
  <gf-confirm-delete-whitelist
    :show="show_delete_popup"
    v-model="show_delete_popup"
    :item="item_to_delete"
    @itemDeleted="deletedItem"
  ></gf-confirm-delete-whitelist>
  <gf-confirm-mail-whitelist
    :show="show_mail_popup"
    v-model="show_mail_popup"
    :item="item_to_mail"
    @itemSent="whitelistMailed"
  ></gf-confirm-mail-whitelist>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      // New creation
      new_role: null,
      new_email: null,
      new_ttl: null,
      ttl_options: [
        "1 days",
        "2 days",
        "3 days",
        "4 days",
        "5 days",
        "6 days",
        "7 days",
      ],
      email_error: false,
      // All
      selected: null,
      role_options: ["TESTER", "USER"],
      whitelist: [],
      // Delete option
      show_delete_popup: false,
      item_to_delete: null,
      item_id_to_delete: null,
      // Mailing options
      show_mail_popup: false,
      item_to_mail: null,
      // To delete
      //   uri: "172.20.10.2",
      uri: "192.168.0.12:8000",
      //   uri:"",
    };
  },
  methods: {
    validateEmail(email) {
      // eslint-disable-next-line
      if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)) {
        return true;
      } else {
        return false;
      }
    },
    resetErrors() {
      this.email_error = false;
    },
    validateWhiteList() {
      if (!this.validateEmail(this.new_email)) {
        this.email_error = true;
        return false;
      }
      if (this.new_role === null) {
        return false;
      }
      if (this.new_ttl === null) {
        return false;
      }
      return true;
    },
    deletedItem(value) {
      if (value) {
        this.item_to_delete = null;
        this.whitelist.splice(this.item_id_to_delete, 1);
      }
    },
    async getWhitelist() {
      try {
        const url = this.$store.state.gf_contracts.backend;
        // const url = "http://192.168.0.12:8000";
        const uri = url + "/get_whitelist/";
        axios.defaults.headers.common["Authorization"] =
          "Token " + this.$store.state.user_auth_w2.token;
        await axios.get(uri).then((res) => {
          if (res.data.whitelist) {
            this.whitelist = res.data.whitelist;
          } else {
            this.whitelist = [];
          }
        });
      } catch (e) {
        console.log("Sign In");
      }
    },
    async createNewWhitelistedUser() {
      const valid = this.validateWhiteList();
      if (valid === false) {
        const notification = {
          type: "error",
          message: "Email, Role and/or TTL is wrong, check it",
        };
        this.$store.dispatch("notification/add", notification, {
          root: true,
        });
      } else {
        const bodyData = new FormData();
        bodyData.append("email", this.new_email);
        bodyData.append("role", this.new_role);
        bodyData.append("expiracy", this.new_ttl + 1);

        try {
          const url = this.$store.state.gf_contracts.backend;
          //   const url = "http://192.168.0.12:8000";
          const uri = url + "/add_to_whitelist/";
          axios.defaults.headers.common["Authorization"] =
            "Token " + this.$store.state.user_auth_w2.token;
          await axios.post(uri, bodyData).then((res) => {
            if (res.data.userExists) {
              const notification = {
                type: "error",
                message: "User already exists.",
              };
              this.$store.dispatch("notification/add", notification, {
                root: true,
              });
              return;
            }
            if (res.data.userAlreadyWhitelisted) {
              const notification = {
                type: "error",
                message: "User already whitelisted.",
              };
              this.$store.dispatch("notification/add", notification, {
                root: true,
              });
              return;
            }
            if (!res.data.token) {
              const notification = {
                type: "error",
                message: "Something happened on server",
              };
              this.$store.dispatch("notification/add", notification, {
                root: true,
              });
              return;
            }
            this.whitelist.push({
              id: res.data.item_id,
              email: this.new_email,
              token: res.data.token,
              role: this.new_role,
              expiracy: res.data.expiry,
            });
            // Resetting the values
            this.new_email = null;
          });
        } catch (e) {
          console.log("Sign In");
        }
      }
    },
    async deleteWhitelistItem(id) {
      //   this.selected = this.filteredWhitelist[id];
      this.item_to_delete = this.filteredWhitelist[id];
      this.item_id_to_delete = id;
      this.show_delete_popup = true;
    },
    timestampToDate(timestamp) {
      var a = new Date(timestamp * 1000);

      // Hours part from the timestamp
      var months = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ];
      var month = months[a.getMonth()];
      var date = a.getDate();
      var ti = new Date(timestamp).toLocaleTimeString("en-US");
      return date + " " + month + "-" + ti;
    },
    async sendEmail(id) {
      this.item_to_mail = this.filteredWhitelist[id];
      this.show_mail_popup = true;
    },
    whitelistMailed() {
      const email = this.item_to_mail.email;
      for (let i = 0; i < this.whitelist.length; i++) {
        if (this.filteredWhitelist[i].email == email) {
          this.whitelist[i].email_sent = true;
        }
      }
      const notification = {
        type: "success",
        message: "Email was sent successfully!",
      };
      this.$store.dispatch("notification/add", notification, {
        root: true,
      });
    },
  },
  computed: {
    filteredWhitelist() {
      return this.whitelist;
    },
  },
  created() {
    this.getWhitelist();
  },
};
</script>

<style scoped>
.no_scroll {
  position: fixed !important;
  transition-duration: 0.5s;
}
.line_sep {
  height: 1px;
  width: 100%;
  background-color: #ebe9e9;
  margin: 20px 0px 20px 0px;
}
.whitelist_external_container {
  position: relative;
  display: flex;
  justify-content: center;
  height: fit-content;
  width: 100%;
  height: fit-content;
  background-color: white;
}
.whitelist_container {
  position: relative;
  display: flex;
  height: fit-content;
  flex-direction: row;
  width: 100%;
  max-width: 80rem;
  box-sizing: border-box;
  padding: 110px 10px 200px 10px;
}
/* Main whitelisting container */
.whitelist_main_container {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  /* max-width: 900px; */
  height: fit-content;
}
.whitelisted_accounts {
  position: relative;
  height: fit-content;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column !important;
  flex-wrap: wrap;
  border-radius: 16px;
  overflow: hidden;
  border: 1px solid #f7f7f7;
}
.whitelisted_account {
  position: relative;
  display: flex;
  flex-direction: row;
  gap: 10px;
  justify-content: space-between;
  width: 100%;
  box-sizing: border-box;
  align-items: center;
  padding: 10px 10px;
}
.whitelisted_account:nth-child(1n) {
  background-color: #f7f7f7;
}
.whitelisted_account:nth-child(2n) {
  background-color: white;
}
.no_whitelist {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  background: #f2f2f2;
  border-radius: 16px;
}
.no_whitelist span {
  font-weight: 500;
  font-size: 16px;
  color: #6e6e73;
  line-height: 24px;
}
.mailed {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.rol_label {
  position: relative;
  display: flex;
  justify-content: flex-start !important;
  align-items: center;
  min-width: 130px;
}
.whitelist_data {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  flex-direction: row;
  flex-wrap: wrap;
  transition-duration: 0.5s;
}
.ctas {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  gap: 10px;
}
/* Toolbar whitelist */
.toobar_whitelist {
  position: relative;
  display: flex;
  height: fit-content;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 0px 0px 30px 0px;
}
.role_selector {
  width: 100%;
  max-width: 150px;
  height: fit-content;
  margin: 0px 0px 5px 0px;
}
.email_input {
  justify-content: center;
  align-items: center !important;
  /* width: 100%; */
  min-width: 300px;
  /* max-width: 350px; */
  height: fit-content;
  flex: 1;
  margin: 0px;
}
.btn_new {
  margin: 0px 0px 5px 0px;
}
@media (max-width: 846px) {
  .whitelisted_account {
    justify-content: center;
    flex-wrap: wrap;
  }
  .rol_label {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .whitelist_data {
    justify-content: center;
  }
  .ctas {
    flex: 1;
    flex-direction: row;
  }
}
@media (max-width: 764px) {
  .email_input,
  .role_selector {
    min-width: 100%;
  }
  .whitelist_container {
    flex-direction: column;
    flex-wrap: wrap;
    max-width: 100%;
  }
  .toobar_whitelist {
    box-sizing: border-box;
    padding: 30px 10px;
    border: 1px solid #ebe9e9;
    border-radius: 16px;
  }
}
</style>
