<template>
  <section class="create__start">
    <div class="create__section">
      <div class="title_text">Create new simple collection</div>
      <div class="generic__text required">
        <h4 class="star">*</h4>
        Required fields
      </div>
      <!-- <div class="subtitle__text">Choose contract type</div> -->
      <div class="generic_label">
        Logo image
        <h4 class="star">*</h4>
      </div>
      <gf-image-drop v-model="this.collection.logo" :showError="e_logo">
        <template v-slot:helper
          >Upload image: JPG, PNG, SVG. Max 2MB
          <h4 class="star">*</h4></template
        >
      </gf-image-drop>
      <!-- {{ collection.value }} {{ collection.logo.size / 1024 }} -->
      <div class="generic_label">Banner image</div>
      <gf-image-drop v-model="collection.banner" :showError="e_banner">
        <template v-slot:helper
          >Upload image: JPG, PNG, SVG. Max 2MB
          <h4 class="star">*</h4>
        </template>
      </gf-image-drop>
      <gf-input-field
        v-model="this.collection.name"
        :showError="e_collection_name"
        :showRequired="true"
      >
        <template v-slot:title>Collection name</template>
        <template v-slot:helper>e.g. "Business summit"</template>
      </gf-input-field>
      <gf-textarea
        v-model="this.collection.description"
        :showError="e_collection_description"
        :showRequired="true"
      >
        <template v-slot:title>Description</template>
        <template v-slot:helper
          >e.g. "Contains the tickets for the summit with added
          utilities"</template
        >
      </gf-textarea>
      <div class="generic_label">
        Blockchain
        <h4 class="star">*</h4>
      </div>
      <gf-select-box-img
        v-model="collection.blockchain"
        :label_text="collection_label"
        :showError="e_collection_blockchain"
        :options="blockchain_type"
      ></gf-select-box-img>
      <div v-if="collection.blockchain">
        <div class="generic_label">
          Payment tokens
          <img
            src="@/assets/icons/question.svg"
            class="help_icon"
            v-if="!payment_help"
            @click="togglePaymentHelp"
          />
          <img
            src="@/assets/icons/question_cancel.svg"
            class="help_icon"
            v-if="payment_help"
            @click="togglePaymentHelp"
          />
        </div>
        <div class="generic__text payment_props" v-if="payment_help">
          e.g. "ETH". These tokens can be used to buy and sell your items
        </div>
        <gf-select-box-img
          v-model="collection.paymentToken"
          :label_text="payment_label"
          :options="payment_tokens[collection.blockchain]"
          :showError="e_collection_payment_token"
        ></gf-select-box-img>
      </div>
      <gf-input-field
        v-model="collection.creatorFee"
        :showError="e_creator_fee"
        :showRequired="true"
        :onlyNumbers="true"
      >
        <template v-slot:title>Creator percentage fee earning</template>
        <template v-slot:helper
          >e.g. "2.5". Collect a fee when some user re-sells the item you
          created. The amount is obtained from the final sale price and will be
          paid monthly to a payout address you had chosen</template
        >
      </gf-input-field>
      <div @click="this.checkCollection()">Calcular</div>

      <div class="properties">
        <div class="subtitle__text">
          Social links
          <img
            src="@/assets/icons/question.svg"
            v-if="!links_help"
            @click="togglePropsHelp"
          />
          <img
            src="@/assets/icons/question_cancel.svg"
            v-if="links_help"
            @click="togglePropsHelp"
          />
        </div>
        <div class="generic__text help_props" v-if="links_help">
          Add all the social links you want so the NFT owners can stay tuned
          with you. New empty fields will be added when the current ones will be
          full.
        </div>
        <div class="properties_container" ref="property_container">
          <gf-social-boxed-input
            v-model="collection.links"
            :social_links="social_links"
          >
          </gf-social-boxed-input>
        </div>
      </div>
      <!-- <gf-contract-selector
        :generator_types="collection_generators"
        v-model="collection.selected_generator"
        :showError="contract_type_error"
      >
      </gf-contract-selector> -->
      <!-- <gf-extra-field-generator
        :extra_fields_data="
          contract_types_fields[collection.selected_generator.generatorType]
        "
        v-model="collection.extra_fields"
      ></gf-extra-field-generator> -->
      <div class="sep"></div>

      <gf-cta-button
        @click="createNFTItem()"
        :label="'Create on server'"
      ></gf-cta-button>
    </div>
  </section>
</template>

<script>
import axios from "axios";
// import "@/assets/contracts/ImageCollectionOnChainABI";
export default {
  data() {
    return {
      // good
      e_logo: false,
      e_banner: false,
      e_collection_name: false,
      e_collection_description: false,
      e_collection_blockchain: false,
      e_collection_payment_token: false,
      e_creator_fee: false,

      // Helpers
      payment_help: false,
      links_help: false,
      // Generators and available types
      generator_types: {},
      blockchain_type: {
        0: {
          network: "Ethereum",
          url: "assets/icons/blockchains/ethereum.png",
        },
        1: { network: "Polygon", url: "assets/icons/blockchains/polygon.png" },
      },
      payment_tokens: {
        Ethereum: {
          0: {
            network: "ETH",
            url: "assets/icons/blockchains/tokens/ethereum_eth.png",
          },
        },
        Polygon: {
          0: {
            network: "MATIC",
            url: "assets/icons/blockchains/tokens/polygon_matic.png",
          },
          1: {
            network: "ETH",
            url: "assets/icons/blockchains/tokens/polygon_eth.png",
          },
        },
      },
      social_links: [
        // {
        //   network: "website",
        //   network_url: "",
        // },
        {
          network: "discord",
          network_url: "",
        },
        {
          network: "instagram",
          network_url: "",
        },
        // {
        //   network: "telegram",
        //   network_url: "",
        // },
      ],
      // Data
      collection_type_label: "Select the contract type",
      collection_label: "Choose where to upload",
      payment_label: "Choose the payment token",

      // Data which should be sent to the server to save
      collection: {
        selected_generator: {
          generatorID: "0",
          generatorType: "SIMPLE_COLLECTION",
          generatorDescription: "",
        },
        logo: "",
        banner: "",
        name: "",
        description: "",
        blockchain: "", // Blochain where it will be posted
        paymentToken: "",
        creatorFee: 0,
        links: [
          {
            network: "website",
            network_url: "",
          },
          {
            network: "discord",
            network_url: "",
          },
          {
            network: "instagram",
            network_url: "",
          },
          {
            network: "telegram",
            network_url: "",
          },
        ],
        extra_fields: [], // Fields which will be added on chain
      },
      nft_image: null,
      data: [],
    };
  },
  props: {
    wallet_account: {
      type: Object,
    },
    wallet_connected: {
      type: Boolean,
      default: true,
    },
  },
  methods: {
    togglePropsHelp() {
      this.links_help = !this.links_help;
    },
    togglePaymentHelp() {
      this.payment_help = !this.payment_help;
    },
    formatSocialLinks(links) {
      console.log("Links 1:", links);
      if (links.at(-1).network_url === "") {
        links = links.slice(0, -1);
        console.log("Links 2:", links);
      } else {
        return links;
      }
    },

    async createNFTItem() {
      if (!this.checkCollection()) {
        return;
      }
      console.log("NFT Image Uploading");
      const fd = new FormData();
      fd.append("username", "0x01234");
      fd.append("name", this.collection.name);
      fd.append("description", this.collection.description);
      fd.append("blockchain", this.collection.blockchain);
      fd.append("payment_token", this.collection.paymentToken);
      fd.append("creator_fee", this.collection.creatorFee);
      // fd.append("social_links", this.collection.links);
      // fd.append("specific_collection", this.collection.extra_fields);
      fd.append("logo", this.collection.logo, this.collection.logo.name);
      // Extra
      fd.append(
        "social_links",
        JSON.stringify(
          this.collection.links.at(-1).network_url === ""
            ? this.collection.links.slice(0, -1)
            : this.collection.links
        )
      );
      fd.append("extra_fields", JSON.stringify(this.collection.extra_fields));
      fd.append("banner", this.collection.banner, this.collection.banner.name);
      // Token
      const token = this.$store.state.user_auth_w2.token;
      let url =
        this.$store.state.gf_contracts.backend +
        "/post_user_collection_simple_collection/";
      axios.defaults.headers.common["Authorization"] = "Token " + token;
      /**
       * Store on a cockie, when user creates its account, it could save
       * things there, so DDos will be not available
       */
      await axios.post(url, fd).then((res) => {
        console.log("Response:", res);
        this.reloadCollections();
        const notification = {
          type: "success",
          message:
            "Your collection was created successfully, now add some NFTS!",
        };
        this.$store.dispatch("notification/add", notification, {
          root: true,
        });
        this.$store.state.account_data.nftForCollectionSelected =
          res.data.coll_id;
        this.$router.push("/account/nft/create_simple_collection");
      });
    },
    sendError(text) {
      const notification = {
        type: "error",
        message: text,
      };
      this.$store.dispatch("notification/add", notification, {
        root: true,
      });
    },
    resetErrors() {
      this.e_logo = false;
      this.e_banner = false;
      this.e_collection_name = false;
      this.e_collection_description = false;
      this.e_collection_blockchain = false;
      this.e_collection_payment_token = false;
      this.e_creator_fee = false;
    },
    checkSizeAndType(imgFile) {
      let maxMB = this.$store.state.constants.imgMaxSize;
      let allowedTypes = this.$store.state.constants.imgAllowedTypes;
      try {
        if (imgFile.size) {
          let sizeMB = imgFile.size;
          if (sizeMB <= maxMB) {
            // Checcking the types
            let ending = imgFile.name.split(".").at(-1);
            if (allowedTypes.includes(ending)) {
              return true;
            } else {
              return false;
            }
          } else {
            this.sendError("Image size is bigger than 2MB");
            return false;
          }
        } else {
          return false;
        }
      } catch (e) {
        return false;
      }
    },
    checkCollection() {
      this.resetErrors();
      let logo_check = this.checkSizeAndType(this.collection.logo);
      let banner_check = this.checkSizeAndType(this.collection.banner);
      let coll_name = this.collection.name.length > 0 ? true : false;
      if (!coll_name) {
        this.e_collection_name = true;
        this.sendError("Collection name is required.");
      }
      let coll_desc = this.collection.description.length > 0 ? true : false;
      if (!coll_desc) {
        this.e_collection_description = true;
        this.sendError("Collection description is required.");
      }
      let coll_blockchain =
        this.collection.blockchain && this.collection.blockchain.length > 0
          ? true
          : false;
      if (!coll_blockchain) {
        this.e_collection_blockchain = true;
        this.sendError("Select the blockchain to post on.");
      }
      let coll_blockchain_type =
        this.collection.paymentToken && this.collection.paymentToken.length > 0
          ? true
          : false;
      if (!coll_blockchain_type) {
        this.e_collection_payment_token = true;
        this.sendError("Collection Payment token is required.");
      }
      let coll_creator_fee =
        this.collection.creatorFee.toString().length > 0 &&
        Number(this.collection.creatorFee) <= 100
          ? true
          : false;

      if (!coll_creator_fee) {
        this.e_creator_fee = true;
        this.sendError("Check the creator fee percentage.");
      }

      let res =
        logo_check &&
        banner_check &&
        coll_name &&
        coll_desc &&
        coll_blockchain &&
        coll_blockchain_type &&
        coll_creator_fee;
      return res;
    },
    checkForm() {
      if (this.file) {
        if (typeof this.nft__name === "string" && this.nft__name != "") {
          return true;
        } else {
          console.log("Wrong name");
          return false;
        }
      } else {
        console.log("File not selected");
        return false;
      }
    },
    resetForm() {
      this.file = null;
      this.nft__name = "";
      this.nft__description = "";
      this.nft__collection = "";
      this.nft__link = "";
    },
    getBase64(file) {
      return new Promise(function (resolve, reject) {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          return resolve(reader.result);
        };
        reader.onerror = () => {
          return reject("F");
        };
      });
    },
    async selectFile() {
      this.isOver = false;
      const file = this.$refs.nft__file.files[0];
      const allowedTypes = [
        "image/jpeg",
        "image/jpg",
        "image/png",
        "image/gif",
        "image/svg+xml",
      ];
      const MAX_FILE_SIZE = 10000000; // KB
      const tooLarge = file.size > MAX_FILE_SIZE;

      if (allowedTypes.includes(file.type) && !tooLarge) {
        this.file = file;
        this.nft__loaded = URL.createObjectURL(this.file);

        console.log("OBJ:", this.nft__loaded);

        // Creating the base64 encoding of the files
        if (this.file.type === "image/gisf") {
          // Create the gif strategy
        } else {
          await this.getBase64(file).then((data) => {
            console.log("Data:", data);
            this.nft__file = data;
          });
        }
      } else {
        if (tooLarge) {
          const msg = `File is too large. Max size is ${
            MAX_FILE_SIZE / 1000000
          }MB`;
          const notification = {
            type: "error",
            message: msg,
          };
          this.$store.dispatch("notification/add", notification, {
            root: true,
          });
        } else {
          const notification = {
            type: "error",
            message: "Only images are allowed",
          };
          this.$store.dispatch("notification/add", notification, {
            root: true,
          });
        }
      }
    },

    deleteFile() {
      this.file = null;
    },
    dragover(event) {
      this.isOver = true;
      event.stopPropagation(); // might not be necessary
      event.preventDefault();
    },
    dragleave(event) {
      this.isOver = false;
      event.stopPropagation(); // might not be necessary
      event.preventDefault();
    },
    setCollection(collection) {
      this.nft__collection = collection;
      console.log("Set:", this.nft__collection);
    },
    scrollToTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    },
    reloadCollections() {
      this.$store.dispatch("account_data/getUserServerCollections", {
        root: true,
      });
    },
  },
  created() {
    this.scrollToTop();
  },
};
</script>

