const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

router.beforeEach((to, from, next) => {
    // Allowed links
    if (to.meta.needsAuth) {
        if (store.state.user_auth_w2.isAuthentificated) {
            // Checking if roles are needed
            if (to.meta.available_roles) {
                if (!to.meta.available_roles.includes(store.state.account_data.data.role)) {
                    // If the role is forbidden
                    next("/account")
                } else {
                    // If the role is good and need auth
                    next()
                }
            } else {
                // When need auth but not role
                next()
            }
        } else {
            // When need auth but it do not has it
            console.log("TO:", to)
            next("/accounts/login")
        }
    } else {
        // Open for everyone
        next()
    }
    //console.log(`Routing from: '${routeFrom.path}' to: '${routeTo.path}'`)
})

export default router