import axios from "axios";

export const namespaced = true

export const state = {
    data: {
        closed_to_commissions: null,
        description: "-",
        has_analytics_subscription: false,
        has_marketplace_subscription: false,
        has_utility_subscription: false,
        joined: "2022-09-13T16:51:49Z",
        role: "TESTER",
        social_networks: "",
        thematics: "",
        username: "-",
        email: "-",
        favorite_templates: []
    },
    user_collection_to_nft_ids: {}, // Used to obtain the collection IDS
    user_server_nfts: [],
    user_server_collections: [],
    user_server_collections_to_id: [],
    nftForCollectionSelected: -1, // Collection type selected
}

export const getters = {
    // -> getters['posts/getX']
}

export const mutations = {
    // -> commit('posts/mutationX') Synchronous
}

export const actions = {
    // -> dispatch('posts/actionX') Asynchronous
    async getUserData({ state, rootState }) {
        let backend = rootState.gf_contracts.backend;
        const token = rootState.user_auth_w2.token;
        let url = backend + "/get_user_info"
        axios.defaults.headers.common["Authorization"] = "Token " + token;
        try {
            await axios.get(url).then((res) => {
                state.data = res.data.user_data
                state.data.favorite_templates = res.data.user_data.favorite_templates ? JSON.parse(res.data.user_data.favorite_templates) : []
            })
        } catch (e) {
            if (e.message === "Request failed with status code 401") {
                // CHECK THIS PART AGAIN 
                // this.dispatch("user_auth_w2/removeToken", { root: true });
                this.$router.push("/accounts/login");
            } else {
                this.$router.push("/account");
            }
        }
    },

    async getUserServerNFTs({ state, rootState }) {
        let backend = rootState.gf_contracts.backend;
        const token = rootState.user_auth_w2.token;
        let url = backend + "/get_user_server_nfts"
        axios.defaults.headers.common["Authorization"] = "Token " + token;
        await axios.get(url).then((res) => {
            //console.log("Data:", res.data.server_nfts)
            if (res.data.server_nfts) {
                state.user_server_nfts = res.data.server_nfts //? JSON.parse(res.data.server_nfts) : []
                    // Obtaining the ids so we can show the ids
                state.user_collection_to_nft_ids = {}
                for (let i = 0; i < res.data.server_nfts.length; i++) {
                    //console.log(res.data.server_nfts[i])
                    if (state.user_collection_to_nft_ids[res.data.server_nfts[i].collection]) {
                        state.user_collection_to_nft_ids[res.data.server_nfts[i].collection].push(res.data.server_nfts[i].id)
                    } else {
                        state.user_collection_to_nft_ids[res.data.server_nfts[i].collection] = []
                        state.user_collection_to_nft_ids[res.data.server_nfts[i].collection].push(res.data.server_nfts[i].id)
                    }
                }
            }
        })
    },

    async getUserServerCollections({ state, rootState }) {
        let backend = rootState.gf_contracts.backend;
        const token = rootState.user_auth_w2.token;
        let url = backend + "/get_user_nft_collections"
        axios.defaults.headers.common["Authorization"] = "Token " + token;
        await axios.get(url).then((res) => {
            state.user_server_collections = res.data.server_collections
                // Obtaining the collections IDS
            state.user_server_collections_to_id = []
            for (let i = 0; i < res.data.server_collections.length; i++) {
                state.user_server_collections_to_id.push(res.data.server_collections[i].id)
            }

        })
    },

    async changeFavoriteTemplates({ state, rootState }, templateID) {
        // Updating the favorites templates
        let to_update = []
        let backend = rootState.gf_contracts.backend;
        const token = rootState.user_auth_w2.token;
        let url = backend + "/update_favorite_templates"
        if (state.data.favorite_templates.includes(templateID)) {
            state.data.favorite_templates.forEach((item) => {
                if (item != templateID) {
                    to_update.push(item)
                }
            })

            // Publishing to server
            const bodyData = new FormData();
            bodyData.append("favorite_templates", JSON.stringify(to_update));
            // Sending the changes
            axios.defaults.headers.common["Authorization"] = "Token " + token;
            await axios.put(url, bodyData).then(() => {
                state.data.favorite_templates = to_update
            })
        } else {
            to_update = state.data.favorite_templates
            to_update.push(templateID)

            // Publishing to server
            const bodyData = new FormData();
            bodyData.append("favorite_templates", JSON.stringify(to_update));
            // Sending the changes
            axios.defaults.headers.common["Authorization"] = "Token " + token;
            await axios.put(url, bodyData).then(() => {
                state.data.favorite_templates = to_update
            })
        }
    },

    async checkLogged({ state }) {
        const role = state.data.role
        if (role != "ADMIN") {
            console.log("F")
        }
    }
}