<template>
  <div class="nft_card_container">
    <div class="nft_card_container_card">
      <div class="img">
        <img :src="nft_data.logo" alt="" />
      </div>
      <div class="nft_status" :class="nftStatus">
        <span>On {{ this.status }}</span>
      </div>
      <div class="nft_title">
        <h1>
          {{
            this.$store.state.gf_contracts.collection_types[
              this.nft_data.collection_type
            ]
          }}
        </h1>
        <gf-option-button class="opc_btn" @click="this.toggleMenu()">
        </gf-option-button>
        <span v-if="this.show_option === true" v-click-away="toggleMenu">
          <div>
            <span class="generic_text">Check & mint</span>
            <img src="@/assets/icons/navbar/go_arrow.svg" alt="" />
          </div>
        </span>
      </div>
      <h2>
        {{ this.cutTitle(this.nft_data.name)
        }}<!--  This is a collection name") }} -->
      </h2>
      <h3>Network: {{ this.nft_data.blockchain }}</h3>
      <div class="thin_sep"></div>
      <div class="prop_link">
        <span class="label_text_blue" @click="this.toggleProp()"
          >See properties</span
        >
      </div>
      <!-- PROPS CONTAINER -->
      <div class="prop_container" :class="{ show_prop: prop_hidden }">
        <div class="close_btn" @click="this.toggleProp()">
          <div>
            <img src="@/assets/icons/navbar/go_arrow_left_dark.svg" alt="" />
          </div>
        </div>
        <div class="prop_relative">
          <div class="prop_scroller">
            {{ nft_data }}
            <div class="banner_img">
              <img
                class="banner_im"
                src="@/assets/branding/logo_banner.svg"
                alt=""
              />
            </div>
            <div class="prop_property">
              <span class="label_text">Description</span>
              <span class="generic_text">{{ nft_data.description }}</span>
            </div>
            <div class="prop_property">
              <span class="label_text">Creator percentage fee</span>
              <span class="generic_text">{{ nft_data.creator_fee }}%</span>
            </div>
            <div class="prop_property">
              <span class="label_text">Social links</span>
              <gf-social-tags
                :social_networks="nft_data.social_links"
              ></gf-social-tags>
            </div>
            <div class="thin_sep"></div>
            <div class="prop_property">
              <span class="label_text">Number Field</span>
              <span class="generic_text"
                >Money your want to share to the project.</span
              >
            </div>
            <div class="prop_property">
              <span class="label_text">About contract type</span>
              <span class="generic_text">{{
                this.$store.state.gf_contracts.collection_descriptions[
                  this.nft_data.collection_type
                ]
              }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mixin as VueClickAway } from "vue3-click-away";
export default {
  mixins: [VueClickAway],
  name: "gf-nft-card-server",
  props: {
    status: {
      type: String,
      default: "server",
    },
    nft_data: Object,
    // url: {
    //   type: String,
    //   default: "/",
    // },
    // nft_name: {
    //   type: String,
    //   default: "My georgeus NFT",
    // },
    // nft_collection: { type: String, default: "My collection" },
    // price: {
    //   type: Number,
    //   default: 0,
    // },
  },
  data() {
    return {
      prop_hidden: false,
      show_option: false,
    };
  },
  methods: {
    capitalize(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    cutTitle(title) {
      try {
        console.log("Tit", title, title.length);
        if (title.length > 20) {
          return title.substring(0, 25) + "...";
        } else {
          return title;
        }
      } catch (e) {
        return "...";
      }
    },
    toggleProp() {
      this.prop_hidden = !this.prop_hidden;
    },
    toggleMenu() {
      this.show_option = !this.show_option;
    },
  },
  computed: {
    nftStatus() {
      return `nft_status_${this.status.trim()}`;
    },
  },
};
</script>

<style scoped>
.label_text_blue {
  color: #0071da;
  font-size: 16px;
  font-weight: 500;
  margin: 0px 0px 0px 5px;
  cursor: pointer;
}
.label_text {
  color: #6e6e73;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
}
.generic_text {
  color: #6e6e73;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
}
.subtitle_text {
  color: #252525;
  font-size: 18px;
  line-height: 24px;
  font-weight: 600;
}
.prop_link {
  position: relative;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin: 0px 0px 10px 0px;
  cursor: pointer;
}
.prop_link span {
  position: relative;
}
.prop_link span::before {
  position: absolute;
  content: "";
  top: 10px;
  left: -10px;
  height: 5px;
  width: 5px;
  background-color: none;
  border-radius: 50%;
  transition-duration: 0.3s;
}
.nft_card_container {
  position: relative;
  height: fit-content;
  box-sizing: border-box;
  display: flex;
  padding: 5px 0px 0px 0px;
  transition-duration: 0.5s;
}
.nft_card_container_card {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 300px;
  height: fit-content;
  /* max-height: 500px; */
  background-color: white;
  border-radius: 16px;
  border: 1px solid #ebe9e9;
  /* -webkit-box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.03);
  box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.03); */
  cursor: pointer;
  transition-duration: 0.5s;
  box-sizing: border-box;
  padding: 10px;
  overflow: hidden;
}
.prop_container {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  box-sizing: border-box;
  padding: 10px;
  transform: translateY(100%);
  transition-duration: 0.5s;
}
.prop_relative {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: white;
  overflow-y: auto;
  overflow-x: hidden;
}
.prop_relative::-webkit-scrollbar {
  width: 10px;
  background: white;
  cursor: pointer !important;
  border-radius: 0px 10px 10px 0px;
  border-left: 1px solid #f2f2f2;
}
.prop_relative::-webkit-scrollbar-thumb {
  background: #6e6e75;
  border-radius: 0px 10px 10px 0px;
  cursor: pointer !important;
}
.close_btn {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 160;
  height: 40px;
  width: 100%;
  bottom: 10px;
  left: 0px;
}
.close_btn div {
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0071da;
  border-radius: 50%;
}
.close_btn div img {
  height: 25px;
  width: 25px;
}
.show_prop {
  transform: translateY(0%);
  transition-duration: 0.5s;
}
.prop_scroller {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  background-color: white;
  height: fit-content;
  min-height: 100%;
  box-sizing: border-box;
  padding: 0px 0px 45px 0px;
  cursor: default;
}
.banner_img {
  position: relative;
  display: flex;
  justify-content: center;
  height: fit-content;
  max-height: 80px;
  object-fit: cover;
}
.banner_im {
  position: relative;
  border-radius: 10px;
  height: auto;
  width: 100%;
}
.prop_property {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: fit-content;
  box-sizing: border-box;
  padding: 15px 0px 0px 0px;
}
.prop_property .generic_text {
  color: #252525;
}
/* CARD CONTAINER */
.nft_card_container_card .img {
  position: relative;
  width: 100%;
  height: 250px;
}
.nft_card_container_card .img img {
  width: 100%;
  height: 100%;
  border-radius: 8px 8px 0px 0px;
  object-fit: cover;
}
.nft_card_container_card .nft_title {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-items: center;
  align-items: center;
  margin: 15px 0px 0px 0px;
}
.nft_card_container_card .nft_title h1 {
  font-size: 14px;
  line-height: 20px;
  margin: 0px 8px 10px 0px;
  font-weight: 400;
  color: #6e6e73;
  max-width: 260px;
  overflow: hidden;
}
.nft_card_container_card .nft_title svg {
  height: 14px;
}
.nft_card_container_card h2 {
  font-size: 18px;
  line-height: 24px;
  font-weight: 600;
  color: #252525;
  max-width: 250px;
  overflow: hidden;
}
.nft_card_container_card h3 {
  font-size: 16px !important;
  color: #6e6e73 !important;
  font-weight: 500 !important;
  line-height: 45px !important;
  overflow: hidden;
}
.opc_btn {
  position: absolute;
  top: -10px;
  right: -12px;
}
.opc_btn span {
  position: relative;
  top: 0px;
}
.opc_btn ~ span {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 30px;
  right: 0px;
  height: fit-content;
  width: 100%;
  background-color: #f2f2f2;
  box-sizing: border-box;
  padding: 10px 20px;
  border-radius: 16px;
  border: 1px solid #ebe9e9 !important;
  /* -webkit-box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.03);
  box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.03); */
  -webkit-box-shadow: -7px 8px 31px -7px rgba(37, 37, 37, 0.3);
  box-shadow: -7px 8px 31px -7px rgba(37, 37, 37, 0.3);
}
.opc_btn ~ span div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  transition-duration: 0.3s;
}
.opc_btn ~ span div img {
  height: 16px;
  width: 16px;
}

/** Type */
.nft_type {
  position: relative;
  display: flex;
  width: 100%;
  height: fit-content;
  background-color: #0071da;
  border-radius: 16px;
  height: 150px;
}
/**NFT status */
.nft_status {
  position: relative;
  width: 100%;
  height: fit-content;
  border-radius: 0px 0px 8px 8px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 2px 10px;
}
.nft_status_blockchain {
  background-color: rgb(22, 160, 0, 0.2) !important;
  color: rgb(22, 160, 0);
}
.nft_status_server {
  background-color: rgb(0, 113, 218, 0.2) !important;
  color: rgb(0, 113, 218);
}
@media (hover: hover) {
  .nft_card_container:hover {
    padding: 0px 0px 5px 0px;
    transition-duration: 0.3s;
  }
  .nft_card_container_card:hover {
    border: 1px solid #ebe9e9 !important;
    -webkit-box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.07);
    box-shadow: 0px 0px 15px 5px rgba(37, 37, 37, 0.07);
  }
  .nft_prop:hover {
    background: #0071da;
    border-radius: 10px;
    transition-duration: 0.3s;
  }
  .nft_prop:hover.nft_prop::before {
    background-color: white !important;
  }
  .nft_prop:hover.nft_prop h1,
  .nft_prop:hover.nft_prop h2 {
    color: white;
  }
  .prop_link:hover span::before {
    background-color: #0071da;
    transition-duration: 0.3s;
  }
  .opc_btn ~ span div:hover span {
    color: #0071da !important;
    transition-duration: 0.3s;
  }
}

@media (max-width: 465px) {
  .nft_card_container {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .nft_card_container_card {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 300px;
  }
}
</style>
